import React, { useState, useEffect } from 'react'
import { HuePicker, SketchPicker as ColorPicker } from 'react-color'

import helpers from '../../helpers/colorConverter'
import Log from '../../controller/Log'
import defaults from '../_defaults/components'


/*
 * ACTIONS
 *
 */
function SwitchAction(props) {
	const { device, stateKey, stateVal } = props;
	
	const onChange = e => {
		setChecked(e.target.checked);
		
		let val = e.target.checked;
		if (stateKey === 'level') {
			val = e.target.checked ? ((device.getOption('defaultValue')) || 100) : 0;
		}
		
		device.setDeviceState(stateKey, val).catch(({ error }) => Log._('Function (light)', error.message, 'error', true, error.stack));
	}
	
	const [checked, setChecked] = useState(stateVal > 0);
	useEffect(() => setChecked(stateVal > 0), [stateVal]);
	
	// only show percentage if device has a switch itself
	if (device.parent && device.parent.states.power) {
		return <defaults.action.Action {...props} stateVal={checked} onChange={onChange} />
	}
	
	return <defaults.action.SwitchAction {...props} stateVal={checked} onChange={onChange} />
}

function LightColorState(props) {
	const { stateKey, stateVal } = props;
	
	const [colorSpace, setColorSpace] = useState(stateVal);
	useEffect(() => setColorSpace(stateVal), [stateVal]);
	
	let colors, labels = {};
	if (stateKey === 'rgb' && colorSpace) {
		colors = colorSpace.split(',');
		labels = { Red: colors[0], Green: colors[1], Blue: colors[2] };
		
	}
	else if (stateKey === 'hsv' && colorSpace) {
		colors = colorSpace.split(',');
		labels = { Hue: colors[0], Saturation: colors[1], Brightness: colors[2] };
	}
	
	const Label = defaults.components.Label;
	let label = [];
	for (let key in labels) {
		label.push(<Label key={key} label={key + ' ' + labels[key]} br={true} />);
	}
	
	return label;
}


/*
 * BODY
 *
 */
function LightTemperatureBody(props) {
	const Level = defaults.body.LevelBody;
	return (
		<Level min={2000} max={6500} step={10} {...props} />
	);
}

function LightHueBody(props) {
	const { device, stateKey, stateVal, title } = props;
	
	const [hue, setHue] = useState(stateVal);
	useEffect(() => setHue(stateVal), [stateVal]);
	
	
	const onChange = (color, e) => {
		setHue(Math.round(color.hsl.h));
	}
	
	const onChangeComplete = (color, e) => {
		setHue(Math.round(color.hsl.h));
		device.setDeviceState(stateKey, Math.round(color.hsl.h)).catch(({ error }) => Log._('Function (light)', error.message, 'error', true, error.stack));
	}
	
	
	const Component = defaults.components.Component;
	const color = helpers.RgbToHex(helpers.HsvToRgb(hue));
	return (

<Component title={title || stateKey }>
	<HuePicker
		onChange={onChange}
		onChangeComplete={onChangeComplete}
		color={color}
		width='100%'
		/>
		
</Component>

	);
}

function LightColorBody(props) {
	const { device, stateKey, stateVal, title } = props;
	
	let initialColor = stateVal || '#ffffff';
	
	// RGB
	if (stateKey === 'rgb') {
		initialColor = helpers.RgbToHex(initialColor);
	}
	
	// HSV
	else if (stateKey === 'hsv') {
		initialColor = helpers.RgbToHex(helpers.HsvToRgb(initialColor));
	}
	
	const [colorSpace, setColorSpace] = useState(initialColor);
	useEffect(() => {
		
		// RGB
		if (stateKey === 'rgb') {
			setColorSpace(helpers.RgbToHex(stateVal));
		}
		
		// HSV
		else if (stateKey === 'hsv') {
			setColorSpace(helpers.RgbToHex(helpers.HsvToRgb(stateVal)));
		}
		
		// HEX
		else if (stateKey === 'hex') {
			setColorSpace(stateVal);
		}
	}, [stateKey, stateVal]);
	
	
	const onChange = color => {
		color = (color && color[stateKey]) || '';
		
		// RGB
		if (stateKey === 'rgb') {
			color = helpers.RgbToHex([color.r, color.g, color.b].join(','));
		}
		
		// HSV
		else if (stateKey === 'hsv') {
			color = helpers.RgbToHex(helpers.HsvToRgb([color.h, color.s, color.v].join(',')));
		}
		
		setColorSpace(color);
		return color;
	}
	
	const onChangeComplete = color => {
		color = onChange(color);
		device.setDeviceState(stateKey, color).catch(({ error }) => Log._('Function (light)', error.message, 'error', true, error.stack));
	};
	
	const Component = defaults.components.Component;
	return (

<Component title={title || stateKey}>
	<ColorPicker
		onChange={onChange}
		onChangeComplete={onChangeComplete}
		color={colorSpace}
		width='100%'
		/>
		
</Component>

	);
}


export default {
	body: {
		LightTemperatureBody,
		LightColorBody,
		LightHueBody
	},
	action: {
		SwitchAction,
		LightColorState
	}
}