import React from 'react'

import { DEFAULTS } from '../../../config/config'
import LayoutsConfig from './Layouts.config'
import { moveKeyInArray, sortArray } from '../../../helpers/utils'

import i18n from '../../../controller/i18n'
//import Devices from '../../../controller/Devices'
import { ModuleConfigurations, ModuleSettings } from '../../../modules/'

import Badge from '@material-ui/core/Badge'
import Grid from '@material-ui/core/Grid'
import IconButton from '@material-ui/core/IconButton'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'

import ArrowUpIcon from '@material-ui/icons/KeyboardArrowUp'
import ArrowDownIcon from '@material-ui/icons/KeyboardArrowDown'
import DeleteIcon from '@material-ui/icons/Delete'
import DevicesIcon from '@material-ui/icons/Devices'
//import DragIcon from '@material-ui/icons/DragIndicator'
import DividerIcon from '@material-ui/icons/Remove'
import GroupIcon from '@material-ui/icons/FormatListBulleted'

import DragDrop from '../../../components/DragDrop'
import Dialog from '../../../components/Dialog'
import Inputs from '../../../components/Inputs'
import Placeholder from '../../../components/Placeholder'


/*
 * STYLES
 */
import { withStyles } from '@material-ui/core/styles'
const styles = theme => ({
	iconContainer: {
		display: 'flex',
		flexDirection: 'column',
		alignItems: 'center'
	},
	iconCentered: {
		verticalAlign: 'middle',
		marginRight: theme.spacing(2)
	},
	noPadding: {
		padding: 0,
	},
	alignRight: {
		display: 'flex',
		justifyContent: 'flex-end',
		
		'& > :not(:first-child)': {
			marginLeft: 8,
		}
	},
	height: {
		lineHeight: '45px'
	},
	title: {
		lineHeight: '25px'
	},
	Badge: {
		right: 'unset',
		transform: 'none',
	}
});


class WidgetPage extends React.Component {
	
	constructor(props) {
		super(props);
		
		this.state = {
			widget: this.props.widget,
			errors: {}
		}
		
		this.editor = { moduleConfig: {}, ...this.props.widget };
		this.deviceList = this.props.devices; // Devices.getDevices({ sort: true });
		this.deviceOptions = sortArray(Object.values(this.deviceList), 'function');
	}
	
	/*
	 *
	 */
	componentDidMount() {
		this._onChange({ error: !this.state.widget.title, id: 'title' }, this.state.widget.title || ''); // trigger initial validation
	}
	
	/**
	 *
	 */
	_addItem = (type, val) => {
		let device = { type: type }
		
		if (type === 'device') {
			device.deviceId = val.id;
		}
		else if (type === 'group') {
			device.name = val;
		}
		
		this.setState(prevStates => {
			let nextErrors = { ...prevStates.errors }
			delete nextErrors.addDevice;
			
			return {
				errors: nextErrors,
				widget: {
					...prevStates.widget,
					devices: [
						...prevStates.widget.devices || [],
						device
					]
				}
			}
		});
	}
	
	/**
	 *
	 */
	_editItem = (index, props, val) => {
		
		// error occured
		if (props.error) {
			this.setState(prevStates => ({
				errors: {
					...prevStates.errors,
					[props.id]: props.error
				}
			}));
		}
		
		// no error -> remove old error
		else if (!props.error && this.state.errors[props.id]) {
			this.setState(prevStates => {
				let nextErrors = { ...prevStates.errors || {} }
				delete nextErrors[props.id];
				return { errors: nextErrors };
			});
		}
		
		//
		this.setState(prevStates => {
			let nextWidget = { ...prevStates.widget }
			nextWidget.devices[index][props.id] = val;
			
			return { widget: nextWidget };
		});
	}
	
	/**
	 *
	 */
	_moveItem = (action, index) => {
		
		this.editor.devices = moveKeyInArray([ ...this.editor.devices || [] ], index, action);
		this.setState(prevStates => ({
			widget: {
				...prevStates.widget,
				devices: moveKeyInArray([ ...prevStates.widget.devices ], index, action)
			}
		}));
	}
	
	/**
	 *
	 */
	_deleteItem = index => {
		
		//this.editor.devices.splice(index, 1); // not necessary for some reason
		this.setState(prevStates => {
			let nextWidget = { ...prevStates.widget };
			nextWidget.devices.splice(index, 1);
			
			return {
				widget: nextWidget
			}
		});
	}
	
	/**
	 *
	 */
	_onChange = (props, val) => {
		
		// error occured
		if (props.error && props.error !== this.state.errors[props.id]) {
			this.setState(prevStates => ({
				errors: {
					...prevStates.errors,
					[props.id]: props.error
				}
			}));
		}
		
		// no error -> remove old error
		else if (!props.error && this.state.errors[props.id]) {
			this.setState(prevStates => {
				let nextErrors = { ...prevStates.errors || {} }
				delete nextErrors[props.id];
				return { errors: nextErrors };
			});
		}
		
		// converted nested IDs to object
		if (props.id.indexOf('moduleConfig.') > -1) {
			this.editor.moduleConfig[props.id.replace('moduleConfig.', '')] = val;
		}
		
		// set value
		else {
			this.editor[props.id] = val;
		}
	}
	
	/**
	 *
	 */
	_onSave = action => {
		
		// errors
		const requiredFields = [{ parameter: 'title', required: true }, { parameter: 'module', required: true }];
		let errors = {};
		
		[ ...requiredFields, ...(ModuleConfigurations[this.state.widget.module] || []) ].forEach(field => {
			if (field.required === true && !this.editor[field.parameter] && !this.editor.moduleConfig[field.parameter]) {
				errors[field.parameter] = true;
			}
		});
		
		if (Object.values(errors).indexOf(true) > -1) {
			this.setState(prevStates => ({ errors: { ...prevStates.errors, ...errors }}));
			//console.error(errors, this.editor);
			return false;
		}
		
		// attach devices
		this.editor.devices = [ ...this.state.widget.devices || [] ];
		this.props.onSave(this.editor);
	}
	
	/*
	 *
	 */
	renderDevice(device, index) {
		const { classes } = this.props;
		
		const moduleSettings = ModuleSettings[this.state.widget.module] || {};
		let Device = device.deviceId;
		let d = {};
		
		let Icon = <DevicesIcon classes={{ root: classes.iconCentered }} />
		let Actions = null;
		
		if (device && device.type === 'device') {
			d = this.deviceList[device.deviceId] || {};
			Device = <span className={classes.title}>{d.name + ' (' + i18n.t(d.function) + ')'}</span>
			
			const deviceWidgetConfig = this.state.widget.devices[index] || {};
			const states = Object.keys(d.states || {}).map(state => ({ [state]: d.states[state].label || state }));
			
			Actions = (!moduleSettings.devicesStates || (moduleSettings.devicesStates && moduleSettings.devicesStates.selectable !== false)) && <>
					{(!moduleSettings.devicesStates || !moduleSettings.devicesStates.selectable || (moduleSettings.devicesStates.selectable && moduleSettings.devicesStates.selectable.indexOf('primaryStateKey') > -1)) &&
						<Inputs.Select
							id="primaryStateKey"
							width={150}
							
							label='Primary State Key'
							selection={states}
							val={deviceWidgetConfig.primaryStateKey !== undefined ? deviceWidgetConfig.primaryStateKey : d.primaryStateKey}
							onChange={(props, val) => this._editItem(index, props, val)}
							disabled={moduleSettings.devicesStates && moduleSettings.devicesStates.disabled && moduleSettings.devicesStates.disabled.indexOf('primaryStateKey') > -1}
							/>}
							
					{(!moduleSettings.devicesStates || !moduleSettings.devicesStates.selectable || (moduleSettings.devicesStates.selectable && moduleSettings.devicesStates.selectable.indexOf('secondaryStateKey') > -1)) ?
						<Inputs.Select
							id="secondaryStateKey"
							width={150}
							
							label='Secondary State Key'
							selection={[ { '': i18n.t('none') }, ...states ]}
							val={deviceWidgetConfig.secondaryStateKey !== undefined ? deviceWidgetConfig.secondaryStateKey : d.secondaryStateKey}
							onChange={(props, val) => this._editItem(index, props, val)}
							disabled={moduleSettings.devicesStates && moduleSettings.devicesStates.disabled && moduleSettings.devicesStates.disabled.indexOf('secondaryStateKey') > -1}
							/> : <Placeholder width={150} />}
				</>
		}
		else if (device && device.type === 'group') {
			Icon = <GroupIcon classes={{ root: classes.iconCentered }} />
			Device = <span className={classes.title}>{device.name}</span>
		}
		else if (device && device.type === 'divider') {
			Icon = <DividerIcon classes={{ root: classes.iconCentered }} />
			Device = <em className={classes.title}>{'(' + i18n.t('divider') + ')'}</em>
		}
		
		return (
			<ListItem key={'Device-' + (device.deviceId || device.type) + '-' + index} divider classes={{ container: classes.height }}>
				<Grid container item xs={1}>
					<div className={classes.iconContainer}>
						<IconButton size="small" onClick={() => this._moveItem('up', index)}>
							<ArrowUpIcon />
						</IconButton>
						{/*<IconButton size="small">
							<DragIcon />
						</IconButton>*/}
						<IconButton size="small" onClick={() => this._moveItem('down', index)}>
							<ArrowDownIcon />
						</IconButton>
					</div>
					<IconButton size="small" onClick={() => this._deleteItem(index)} style={{ height: '30px', margin: 'auto 0 auto 8px' }}>
						<DeleteIcon />
					</IconButton>
				</Grid>
				<Grid item xs={6}>
					{moduleSettings.DeviceBodyConfiguration ? React.createElement(moduleSettings.DeviceBodyConfiguration, { device: d, options: (this.editor.devices && this.editor.devices[index]) || device, onChange: (props, val) => this._editItem(index, props, val), elements: { Icon, Device, Actions }}) : <>{Icon} {Device}</>}
				</Grid>
				<Grid item xs={5}>
					{moduleSettings.DeviceActionsConfiguration ? React.createElement(moduleSettings.DeviceActionsConfiguration, { device: d, options: (this.editor.devices && this.editor.devices[index]) || device, onChange: (props, val) => this._editItem(index, props, val), elements: { Icon, Device, Actions }}) : Actions}
				</Grid>
			</ListItem>
		);
	}
	
	/*
	 *
	 */
	renderDeviceList(devices) {
		const { classes } = this.props;
		const renderedDevices = devices.map((device, i) => this.renderDevice(device, i));
		
		return (
			<DragDrop>
				<List classes={{ root: classes.fullWidth }} dense disablePadding>
					{renderedDevices}
				</List>
			</DragDrop>
		);
	}
	
	/*
	 *
	 */
	render() {
		const { classes } = this.props;
		
		if (this.state.widget === null) {
			return null;
		}
		
		// get module configuration options
		const moduleConfig = !this.state.widget.module ? []: ModuleConfigurations[this.state.widget.module || DEFAULTS.MODULE].map(config => {
			
			// consider required fields
			if (!this.state.errors['moduleConfig.' + config.parameter] && config.required === true && !(this.editor.moduleConfig[config.parameter] || config.val)) {
				this._onChange({ error: true, id: 'moduleConfig.' + config.parameter }, (this.editor.moduleConfig[config.parameter] || config.val) || ''); // trigger initial validation
			}
			
			// render config option
			const Input = Inputs[config.type || 'Text'];
			return (
				<Inputs.FieldsetItem columns={4} key={'FieldsetItem-' + config.parameter}>
					<Input
						{...config}
						key={'moduleConfig-' + config.parameter}
						id={'moduleConfig.' + config.parameter}
						label={config.label || config.parameter}
						info={config.description}
						
						onChange={this._onChange}
						val={this.editor.moduleConfig[config.parameter] !== undefined ? this.editor.moduleConfig[config.parameter] : config.val}
						/>
					
				</Inputs.FieldsetItem>
			);
		});
		
		// list devices
		const moduleSettings = ModuleSettings[this.state.widget.module] || {};
		const devices = !this.state.widget.module || moduleSettings.devices === false ? null : this.renderDeviceList(this.state.widget.devices || []);
		const deviceList = (this.state.widget.devices && this.state.widget.devices.map(device => device.deviceId)) || [];
		
		return (

	<Dialog
		header={i18n.t('Widget configuration')}
		classes={{ CustomDialogActions: classes.DialogActions }}
		actions={
			<Grid container>
				<Grid item xs={6}>
					{this.state.widget.index !== undefined && <Inputs.Button key='action-delete' variant="text" onClick={() => this.props.onDelete(this.state.widget.index)} color="secondary" startIcon={<DeleteIcon />} label="Delete Widget" />}
					&nbsp;
				</Grid>
				<Grid item xs={6} className={classes.alignRight}>
					<Inputs.Button key='action-cancel' variant="text" onClick={() => this.props.onClose()} color="secondary" label="Cancel" />
					<Inputs.Button key='action-save' variant="text" onClick={this._onSave} color="primary" disabled={Object.keys(this.state.errors).length > 0} label="Apply" />
				</Grid>
			</Grid>
		}
		
		dense
		maxWidth={'lg'}
		open={true}
		onClose={() => this.props.onClose()}
		>
		
		<Inputs.Fieldset label='Widget-Box'>
			<Inputs.FieldsetItem columns={4}>
				<Inputs.Text
					key='title'
					id='title'
					{...LayoutsConfig.widgetTitle}
					
					autoFocus
					onChange={this._onChange}
					val={this.state.widget.title || ''}
					/>
					
			</Inputs.FieldsetItem>
			<Inputs.FieldsetItem columns={4}>
				<Inputs.Text
					key='icon'
					id='icon'
					{...LayoutsConfig.widgetIcon}
					
					onChange={this._onChange}
					val={this.state.widget.icon || ''}
					/>
					
			</Inputs.FieldsetItem>
			<Inputs.FieldsetItem columns={4}>
				<Inputs.Text
					key='iconStyle'
					id='iconStyle'
					{...LayoutsConfig.widgetIconStyle}
					
					onChange={this._onChange}
					val={this.state.widget.iconStyle ? JSON.stringify(this.state.widget.iconStyle) : ''}
					/>
					
			</Inputs.FieldsetItem>
		</Inputs.Fieldset>
		
		<Inputs.Fieldset label='Module'>
			<Inputs.FieldsetItem>
				<Inputs.Text
					key='module'
					id='module'
					{...LayoutsConfig.widgetModule}
					
					autoComplete
					autoHighlight
					
					val={this.state.widget.module}
					onChange={(props, val) => {
						this._onChange(props, val);
						this.setState(prevStates => ({
							widget: {
								...prevStates.widget,
								module: val
							}
						}));
					}}
					/>
				
			</Inputs.FieldsetItem>
			{moduleConfig}
			
		</Inputs.Fieldset>
		
		{(this.state.widget.module && moduleSettings.devices !== false) && <Inputs.Fieldset label='Devices'>
			
			<Inputs.FieldsetItem columns={3}>
				<Inputs.Text
					width="90%"
					key='addDevice'
					id='addDevice'
					label='Add device'
					
					error={this.state.errors.addDevice !== undefined}
					errorMessage={this.state.errors.addDevice || ''}
					
					autoComplete
					autoHighlight
					disableCloseOnSelect
					clearOnSelect
					clearOnBlur
					
					options={this.deviceOptions}
					getOptionLabel={option => option.function + ' ' + option.name + (!option.attributes || !option.attributes.manufacturer ? '' : ' (' + option.attributes.manufacturer.name + ')')}
					renderOption={(option, state) => {
						const added = deviceList.indexOf(option.id) > -1 ? deviceList.filter(device => device === option.id).length : 0;
						return <>
							<Badge badgeContent={added} classes={{ badge: classes.Badge }} color="primary">
								<span style={{ fontSize: '0.8rem', paddingLeft: '24px' }}>{added > 0 ? <strong>{option.name}</strong> : option.name}</span>
							</Badge>
						</>
					}}
					
					onChange={(props, device) => {
						
						// check if an item has been selected
						if (device) {
							
							// verify if device has certain state, if required by the module
							const states = Object.keys(device.states);
							if (moduleSettings.devicesStates && moduleSettings.devicesStates.requiredStates && Array.isArray(moduleSettings.devicesStates.requiredStates) && moduleSettings.devicesStates.requiredStates.some(state => states.indexOf(state) === -1)) {
								
								this.setState(prevStates => ({
									errors: {
										...prevStates.errors,
										'addDevice': i18n.t('Device needs one of the following states') + ': ' + moduleSettings.devicesStates.requiredStates.join(', ')
									}
								}));
							}
							else {
								this._addItem('device', device);
							}
						}
					}}
					
					groupBy={option => option.function}
					closeIcon={null}
					popupIcon={null}
					/>
				
			</Inputs.FieldsetItem>
			{Array.isArray(moduleSettings['devices']) && moduleSettings['devices'].indexOf('group') > -1 && <><Inputs.FieldsetItem columns={1} centered>
				<em>{i18n.t('or')}</em>
			</Inputs.FieldsetItem>
			<Inputs.FieldsetItem columns={3}>
				<Inputs.Text
					width="90%"
					key='addGroup'
					id='addGroup'
					label='Add group'
					
					onEnter={(props, val) => val && this._addItem('group', val)}
					clearOnEnter
					clearOnBlur
					/>
				
			</Inputs.FieldsetItem></>}
			{Array.isArray(moduleSettings['devices']) && moduleSettings['devices'].indexOf('divider') > -1 && <><Inputs.FieldsetItem columns={1} centered>
				<em>{i18n.t('or')}</em>
			</Inputs.FieldsetItem>
			<Inputs.FieldsetItem columns={3}>
				<Inputs.Button
					key='addDivider'
					id='addDivider'
					label='Add divider'
					onClick={() => this._addItem('divider')}
					/>
				
			</Inputs.FieldsetItem></>}
			<Inputs.FieldsetItem>
				{devices}
			</Inputs.FieldsetItem>
			
		</Inputs.Fieldset>}
	</Dialog>
	
		)
	}
}

export default withStyles(styles)(WidgetPage);
