import React from 'react'
//import clsx from 'clsx'

import i18n from '../controller/i18n'

import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import Hidden from '@material-ui/core/Hidden'
import IconButton from '@material-ui/core/IconButton'
import Tooltip from '@material-ui/core/tooltip'
//import Badge from '@material-ui/core/Badge'
//import NotificationsIcon from '@material-ui/icons/Notifications'
//import MenuIcon from '@material-ui/icons/Menu'
//import DonateIcon from '@material-ui/icons/Favorite'
import SettingsIcon from '@material-ui/icons/Settings'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	appBar: {
		flexGrow: 1,
		boxShadow: 'none',
		transform: 'none',
	},
	toolbar: {
		backgroundColor: theme.palette.primary.main
	},
	menuButton: {
		marginRight: theme.spacing(2)
	},
	title: {
		flexGrow: 1,
	},
	notConnected: {
		color: '#f30'
	}
}));


export default function TopBar(props) {
	const classes = useStyles();
	const { children, title, connectionStatus, navigate } = props;
	
	const connectionStatusIcon = connectionStatus === true ? 'lan-connect' : (connectionStatus === 'pending' ? 'lan-pending' : 'lan-disconnect');
	const connectionStatusTooltip = connectionStatus === true ? i18n.t('Connected to ioBroker') : (connectionStatus === 'pending' ? (i18n.t('Not connected to ioBroker') + '! ' + i18n.t('Reconnecting') + '..') : (i18n.t('Not connected to ioBroker') + '! ' + i18n.t('Connection failed') + '!'));
	
	return (

<AppBar id="appBar" position="static" className={classes.appBar}>

	<Toolbar className={classes.toolbar}>
		
		<Typography variant="h6" className={classes.title}>
			{title}
		</Typography>
		
		<Hidden smDown>
			{connectionStatus !== true && <IconButton classes={{ root: classes.notConnected }} color="inherit" onClick={() => navigate('logs')}>
				<Tooltip title={connectionStatusTooltip}>
					<span className={"mdi mdi-" + connectionStatusIcon}></span>
				</Tooltip>
			</IconButton>}
		{/*
			<IconButton color="inherit" onClick={() => navigate('donate')}>
				<DonateIcon />
			</IconButton>
		*/}
			<IconButton color="inherit" onClick={() => navigate('configuration')}>
				<SettingsIcon />
			</IconButton>
		</Hidden>
		
		{/*
		<IconButton color="inherit" onClick={() => toggleDrawer('notificationDrawer', true)}>
			<Badge badgeContent={notifications} color="secondary">
				<NotificationsIcon />
			</Badge>
		</IconButton>
		*/}
		
	</Toolbar>
	
	{children}
</AppBar>

	);
}
