import React, { useState, useEffect, useRef } from 'react'

import { format } from 'date-fns'
import { getSunrise, getSunset } from 'sunrise-sunset-js'

import CircularProgress from '@material-ui/core/CircularProgress'

import { joinConfig } from '../../helpers/utils'
import i18n from '../../controller/i18n'
import Log from '../../controller/Log'
import Settings from '../../controller/Settings'


export default function DateTime(props) {
	let config = joinConfig(props.config, require('./DateTime.config.js').default);
	config.refresh = config.format.indexOf('s') > -1 ? 1000 : (config.format.indexOf('m') > -1 ? 60*1000 : 60*60*1000);
	config.date = format(new Date(), config._defaults.format, { locale: i18n.getLocale() });
	
	try {
		config.date = format(new Date(), config.format, { locale: i18n.getLocale() });
	}
	catch(err) {
		Log._('DateTime', i18n.t('Incorret DateTime parameter provided') + '!', 'error', true, err.stack);
		config.format = config._defaults.format;
	}
	
	const refTimer = useRef();
	const [DateTime, setDateTime] = useState(config.date);
	const [city, setCity] = useState(null);
	const [sunrise, setSunrise] = useState(null);
	const [sunset, setSunset] = useState(null);
	
	useEffect(() => {
		
		function refresh() {
			
			// set current time
			setDateTime(format(new Date(), config.format, { locale: i18n.getLocale() }));
		
			// set sunrise / sunset
			if (Settings.getSetting('iobroker').latitude && Settings.getSetting('iobroker').longitude) {
				setSunrise(getSunrise(Settings.getSetting('iobroker').latitude, Settings.getSetting('iobroker').longitude));
				setSunset(getSunset(Settings.getSetting('iobroker').latitude, Settings.getSetting('iobroker').longitude));
			}
			
			// constantly refresh
			refTimer.current = setTimeout(refresh, config.refresh);
		}
		
		// refresh current time
		clearTimeout(refTimer.current);
		refTimer.current = setTimeout(refresh, 1*1000); // first refresh some seconds after render
		
		if (Settings.getSetting('iobroker').city) {
			setCity(Settings.getSetting('iobroker').city);
		}
		
		// unmount
		return () => clearTimeout(refTimer.current);
		
	}, [config.format, config.refresh]);
	
	return (

<div style={config.style}>
	{i18n.t('%DateTime').replace(/%DateTime/, DateTime)}
	
	{config.sun !== false && <div style={{ margin: '5px 0', fontSize: '1.2rem' }}>
		
		<span className="mdi mdi-city-variant-outline"></span> {city ? city : <CircularProgress size="1.2rem" />}
		<span style={{ margin: '0 10px' }}></span>
		
		<span className="mdi mdi-weather-sunset-up"></span> {sunrise ? format(sunrise, 'HH:mm') : <CircularProgress size="1.2rem" />}
		<span style={{ margin: '0 10px' }}></span>
		
		<span className="mdi mdi-weather-sunset-down"></span> {sunset ? format(sunset, 'HH:mm') : <CircularProgress size="1.2rem" />}
	</div>
	}
	
</div>

	);
}
