import i18n from '../controller/i18n'


export const ModuleList = [
	'AdapterStatus',
	'Chart',
	'DateTime',
	'iFrame',
	'Map',
	//'Multimedia',
	'StateList',
	'StateListHorizontal',
	//'Weather'
];


let _ = {}
let Settings = {}
let Configurations = {}

ModuleList.forEach(module => {
	
	// load module language file
	try {
		i18n.setTranslations(language => require('./' + module + '/i18n/' + language));
	}
	catch(err) {
		//console.error(err);
	}
	
	// load module settings
	Settings[module] = require('./' + module + '/' + module + '.settings').default;
	
	// load module configurations
	Configurations[module] = require('./' + module + '/' + module + '.config').default;
	
	// load module itself
	_[module] = require('./' + module + '/' + module).default;
});

export const ModuleSettings = Settings;
export const ModuleConfigurations = Configurations;
export default _;
