import getActivityType from './blind-manufacturers'

export default {
	"level": {
		"body": "LevelBody",
		"action": "BlindLevelAction",
		
		"value": val => Math.round(val),
		"unit": " %",
		"icon": {
			"default": "window-shutter-open",
			">90": "window-shutter-open",
			"<=90": "window-shutter"
		}
	},
	"level2": {
		"body": "LevelBody",
		"action": "BlindLevelAction",
		
		"value": val => Math.round(val),
		"unit": " %",
		"icon": {
			"default": "window-shutter-open",
			">90": "window-shutter-open",
			"<=90": "window-shutter"
		}
	},
	"activity": {
		"body": null,
		"action": null,
		
		"val": (val, stateKey, device) => getActivityType(device, stateKey, val),
		"icon": {
			"true": "pan-vertical",
			"false": "dots-vertical"
		},
		"display": {
			"true": "blind#activity#true",
			"false": "blind#activity#false"
		}
	}
}