import React from 'react'
import clsx from 'clsx'

import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import ListItemText from '@material-ui/core/ListItemText'

import { components } from '../../functions'
import i18n from '../../controller/i18n'
import Log from '../../controller/Log'


/*
 * STYLES
 */
import { withStyles } from '@material-ui/core/styles';
const styles = theme => ({
	defaultListIcon: props => ({
		...theme.components.icon,
		...props.styles
	}),
	horizontalListItem: {
		display: 'inline-block',
		textAlign: 'center',
		paddingRight: '16px',
		width: '140px',
		marginTop: '10px'
	},
	horizontalListItemIcon: {
		minWidth: 'auto'
	},
	horizontalListItemAction: {
		textAlign: 'center',
		top: 0,
		right: 0,
		position: 'static',
		transform: 'none',
	},
	verticalListItemAction: {
		textAlign: 'right'
	},
});


const ListItemIconElement = withStyles(styles)(props => {
	const { customClassNames, children, classes } = props;
	
	let classNames = Array.isArray(customClassNames) ? customClassNames : [customClassNames];
	classNames = classNames.map(className => (classes[className] ? classes[className] : className));
	
	return <span className={clsx(classNames)}>{children}</span>
});


class StateListItem extends React.Component {
	
	constructor(props) {
		super(props);
		
		this.state = {
			ListItemTextWidth: '100%',
			...(this.props.device._isDevice && this.props.device.getDeviceStates()) || {}
		}
		
		this.ListItemText = React.createRef({});
		this.ListItemSecondaryAction = React.createRef({});
	}
	
	componentDidMount() {
		Log._('StateListItem', 'componentDidMount', 'development');
		
		if (this.ListItemText.current) {
			this.ListItemTextWidth = Math.floor(this.ListItemText.current.offsetWidth);
			this._resizeComponent();
		}
		
		// get state changes
		this.props.device._isDevice && this.props.device.listen(this.stateChange);
	}
	
	componentWillUnmount() {
		Log._('StateListItem', 'componentWillUnmount', 'development');
		
		this.props.device._isDevice && this.props.device.unlisten(this.stateChange);
	}
	
	componentDidUpdate() {
		Log._('StateListItem', 'componentDidUpdate', 'development');
		
		this._resizeComponent();
	}
	
	stateChange = (stateKey, state) => this.setState({ [stateKey]: state });
	
	_openDialog = event => {
		if (this.props.openDialog && (event.target.className.indexOf('MuiListItem') > -1 || event.target.className.indexOf('ListIcon') > -1)) {
			this.props.openDialog(this.props.device);
		}
	}
	
	_resizeComponent() {
		const ListItemSecondaryActionWidth = this.ListItemSecondaryAction.current ? Math.floor(this.ListItemSecondaryAction.current.offsetWidth*1.08) : 0;
		
		if (this.state.ListItemSecondaryActionWidth !== ListItemSecondaryActionWidth) {
			this.setState({
				ListItemSecondaryActionWidth: ListItemSecondaryActionWidth,
				ListItemTextWidth: this.ListItemTextWidth-ListItemSecondaryActionWidth + 'px'
			});
		}
	}
	
	render() {
		//Log._('StateListItem', [ 'RENDER render()', this.props.device.name, this.props ], 'development');
		const { classes, config, device, options } = this.props;
		
		// verify device
		if (!this.props.device._isDevice) {
			Log._('StateListItem', i18n.t('Incorrect device assigned to widget using StateList module') + '!', 'warn');
			return null;
		}
		
		// Icon
		options.primaryStateKey = options.primaryStateKey || device.primaryStateKey;
		const primaryState = this.state[options.primaryStateKey] || {};
		const icon = device.getIcon(options.primaryStateKey, primaryState.val) || device.getIcon(options.primaryStateKey, primaryState.value);
		
		// set defaults
		if (config.isDeviceStateDetails) {
			options.label = device.states[options.primaryStateKey].label;
			
			// Body
			options.bodyStateKey = options.primaryStateKey;
			options.bodyElement = device.configurations[options.bodyStateKey.toLowerCase()] && device.configurations[options.bodyStateKey.toLowerCase()].body;
			
			// Action / State
			options.actionElement = device.configurations[options.primaryStateKey.toLowerCase()] && device.configurations[options.primaryStateKey.toLowerCase()].action;
			options.actionType = options.actionElement ? 'action' : 'state';
		}
		
		// Body
		let primaryBody = options.label || device.name;
		if (options.bodyStateKey && options.bodyElement) {
			const BodyElement = (components[device.function] && components[device.function].body[options.bodyElement]) || (components['_defaults'] && components['_defaults'].body[options.bodyElement]) || null;
			primaryBody = BodyElement && <BodyElement title={primaryBody} key={'BodyElement-' + device.id} device={device} state={this.state[options.bodyStateKey] || {}} stateVal={this.state[options.bodyStateKey] && this.state[options.bodyStateKey].val} stateKey={options.bodyStateKey} />
		}
		
		// State
		let Action = null;
		if (!options.actionType || options.actionType === 'state') {
			const StateElement = components['_defaults'].action['State'];
			const primaryAction = <StateElement key={'StateListItem-PrimaryState-' + device.id + '-' + options.primaryStateKey} device={device} state={primaryState} stateVal={primaryState.val} stateKey={options.primaryStateKey} primary />
			const secondaryActions = options.secondaryStatesKeys ? options.secondaryStatesKeys.map((secondaryStateKey, index) => <span key={'StateListItem-SecondaryState' + index + '-' + device.id + '-' + secondaryStateKey}>{(index > 0 && ', ')}<StateElement device={device} state={this.state[secondaryStateKey] || {}} stateVal={this.state[secondaryStateKey] && this.state[secondaryStateKey].val} stateKey={secondaryStateKey} secondary /></span>) : [];
			Action = [ primaryAction, <br key={'StateListItem-Br-' + device.id + '-' + options.primaryStateKey} />, secondaryActions ];
		}
		
		// Action
		else if (options.actionType === 'action') {
			const ActionElement = (components[device.function] && components[device.function].action[options.actionElement]) || (components['_defaults'] && components['_defaults'].action[options.actionElement]) || null;
			const primaryAction = ActionElement && <ActionElement key={'StateListItem-ActionState-' + device.id + '-' + options.primaryStateKey} device={device} state={primaryState} stateVal={primaryState.val} stateKey={options.primaryStateKey} />
			Action = primaryAction;
		}
		
		return (

<React.Fragment>
	{config.divider && config.divider !== 'after' && <ListItem divider className={classes.defaultListDivider} />}
	
	<ListItem
		className={clsx(this.props.openDialog && classes.finger, config.horizontal && classes.horizontalListItem)}
		button={this.props.openDialog !== null}
		onClick={this._openDialog}
		>
		
		<ListItemIcon className={clsx(config.horizontal && classes.horizontalListItemIcon)}>
		{icon === null ? <React.Fragment /> :
			<ListItemIconElement customClassNames={['defaultListIcon','mdi ' + (icon.indexOf('mdi-') === -1 ? 'mdi-' + icon : icon)]} styles={device.getStyle('icon', options.primaryStateKey, primaryState.val)} />
		}
		</ListItemIcon>
		
		<ListItemText
			ref={this.ListItemText}
			disableTypography={config.isDeviceStateDetails}
			primary={primaryBody}
			secondary={(!config.isDeviceStateDetails && typeof primaryBody === 'string' && primaryState && primaryState.timeChanged) || null}
			style={{ flex: 'initial', width: this.state.ListItemTextWidth }}
			/>
		
		<ListItemSecondaryAction
			ref={this.ListItemSecondaryAction}
			className={config.horizontal ? classes.horizontalListItemAction : classes.verticalListItemAction}
			>
			
			{Action}
		</ListItemSecondaryAction>
	</ListItem>
	
	{config.divider && config.divider === 'after' && <ListItem divider style={{margin: '5px 0'}} />}
</React.Fragment>

		);
	}
}

export default withStyles(styles)(StateListItem);
