import { SUPPORTED_LANGUAGES } from '../config/config'
import Log from './Log'


/**
 * Internationalization
 * Based on https://raw.githubusercontent.com/ioBroker/adapter-react/master/src/i18n.js.
 * Copyright 2018-2019 bluefox <dogafox@gmaiLog.com>
 *
 * MIT License
 *
 *
 */
export default class i18n {

	/*
	 * holds the translations
	 */
	static translations = {};

	/*
	 * holds the locales
	 */
	static locales = {};

	/*
	 * default language
	 */
	static language = (navigator.language || navigator.userLanguage || 'en-GB');

	/**
	 * Override default language.
	 *
	 * @param	{String}	language
	 * @return	{Boolean}
	 */
	static setLanguage(language) {
		if (language && i18n.language !== language) {
			Log._('i18n', i18n.t('Set language to %languageShort').replace('%languageShort', language), 'debug');
			i18n.language = language;
			return true;
		}
		
		return false;
	}
	
	/**
	 * Sets all available translation for each supported language.
	 *
	 * @param	{Function}	language
	 * @return	{Boolean}
	 */
	static setTranslations(file) {
		
		SUPPORTED_LANGUAGES.forEach(language => {
			i18n.setTranslation(language, file);
		});
	}
	
	/**
	 * Set a translation for language.
	 *
	 * @param	{String}	language
	 * @param	{Object}	translation
	 * @return	{Boolean}
	 */
	static setTranslation(language, translation) {
		
		// load translation file
		if (typeof translation === 'function') {
			let file;
			
			try {
				file = translation(language);
				translation = file.default;
			}
			catch(err) {
				Log._('i18n', i18n.t('Could not load translation "%translation" file (%errorMessage)').replace('%translation', translation).replace('%errorMessage', err.message), 'warn');
				return false;
			}
		}
		
		// set translation
		if (language && translation) {
			i18n.translations[language] = { ...i18n.translations[language] || {}, ...translation };
			return true;
		}
		
		return false;
	}
	
	/**
	 * Get locale for DateTime based on current language.
	 *
	 * @param	void
	 * @return	{Object}
	 */
	static getLocale(language = null) {
		language = language || i18n.getLanguage(true);
		
		// try to load long language format, e.g. de-DE or en-GB
		if (!i18n.locales[language]) {
			
			try {
				let locale = require('date-fns/locale/' + language);
				i18n.locales[language] = locale.default;
			}
			catch(err) {
				//Log._('i18n', 'Could not load locale for language ' + language + '!', 'warn');
			}
		}
		
		// try to load long language format, e.g. de or en
		if (!i18n.locales[language]) {
			
			try {
				let locale = require('date-fns/locale/' + i18n.getLanguage());
				i18n.locales[language] = locale.default;
			}
			catch(err) {
				Log._('i18n', i18n.t('Could not load locale for language %languageShort or %languageLong').replace('%languageShort', language).replace('%languageLong', i18n.getLanguage()) + '!', 'warn');
				return false;
			}
		}
		
		return i18n.locales[language];
	}
	
	/**
	 * Get current set language.
	 *
	 * @param	{Boolean}	isoLong
	 * @return	{String}
	 */
	static getLanguage(isoLong = false) {
		return i18n.language.substr(0, isoLong ? 5 : 2);
	}
	
	/**
	 * Get current set language.
	 *
	 * @param	{String|Object}		word
	 * @param	{Object}			[options={}]
	 * @param	{Boolean}			[options.ucFirst=false]
	 * @param	{Object}			[placeholders={}]
	 * @param	{String}			[language=i18n.getLanguage()]
	 * @return	{String}
	 */
	static t(word = '', options = {}, placeholders = {}, language = i18n.getLanguage()) {
		
		// if object is provided, it might be translated already
		if (typeof word === 'object') {
			
			if (word[language] !== undefined) {
				word = word[language];
			}
			else {
				let unknown = Object.values(word);
				word = unknown.length > 0 ? unknown[0] : '(unknown word to translate)';
			}
		}
		
		// skip numbers
		if (Number.isNaN(parseFloat(word)) === false) {
			return word;
		}
		
		// if array, then multiple words are given
		if (Array.isArray(word)) {
			word = word.map(w => i18n.t(w, options, placeholders, language)).join('');
		}
		
		// skip English as that is the default
		if (language === i18n.getLanguage() && i18n.getLanguage() === 'en' && word.indexOf('#') === -1) {
			return word;
		}
		
		
		// otherwise, treat as string
		else if (word) {
			word = word.toString();
			if (i18n.translations[language]) {
				
				// check if word is concatenated
				if (word.indexOf('#') > -1) {
					
					let parts = word.split('#');
					while (parts.length > 0 && !i18n.translations[language][parts.join('#')]) {
						parts.shift();
					}
					
					word = parts.join('#');
				}
				
				if (i18n.translations[language][word]) {
					word = i18n.translations[language][word];
				}
				else if (i18n.translations[language][word.toLowerCase()]) {
					word = i18n.translations[language][word.toLowerCase()];
				}
				else {
					//Log._('i18n', i18n.t('Word "%word" not found in language %language').replace('%language', language).replace('%word', word) + '!', 'debug'); // causes recursion
					Log._('i18n', ('Word "%word" not found in language %language').replace('%language', language).replace('%word', word) + '!', 'debug');
				}
			}
			else {
				Log._('i18n', i18n.t('Language %language not loaded for word "%word"').replace('%language', language).replace('%word', word) + '!', 'error');
			}
		}
		
		// replace placeholders
		for (let key in placeholders) {
			word = word.replace(RegExp('%' + key + '%', 'g'), placeholders[key]);
		}
		
		// options
		if (options.ucFirst) {
			word = word.charAt(0).toUpperCase() + word.slice(1);
		}
		
		return word;
	}
}
