import React from 'react'
import i18n from '../../controller/i18n'

import AddIcon from '@material-ui/icons/AddCircle'
import CircularProgress from '@material-ui/core/CircularProgress'
import InputButton from '@material-ui/core/Button'

import Field from './Field'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles((theme) => ({
	InputButton: {
		width: '100%'
	}
}));


/*
 *
 */
export default function Button(props) {
	const { id, label, color, variant, disabled, loading, startIcon, onClick } = props;
	const classes = useStyles(props);
	
	const icon = variant !== 'text' && startIcon === undefined ? <AddIcon /> : startIcon;
	
	return (
		<Field {...props} width={variant === 'text' ? 'auto' : props.width}>
			<InputButton
				key={id}
				classes={{ root: classes.InputButton }}
				variant={variant || 'contained'}
				color={color || 'primary'}
				disabled={disabled}
				startIcon={loading ? <CircularProgress size={24} /> : (icon || null)}
				onClick={onClick}
				>
				
				{i18n.t(label)}
			</InputButton>
		</Field>

	)
}