import React, { useState } from 'react'

import i18n from '../../controller/i18n'
import Inputs from '../../components/Inputs'
import { components } from '../../functions'

import Checkbox from '@material-ui/core/Checkbox'
import Divider from '@material-ui/core/Divider'
import Grid from '@material-ui/core/Grid'
import Switch from '@material-ui/core/Switch'

import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank'
import CheckBoxIcon from '@material-ui/icons/CheckBox'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
	wrap: { 
		display: 'inherit'
	},
	Divider: {
		margin: '0 -16px 0 -16px'
	}
}));


export default {
	devices: ['device', 'divider', 'group'],
	DeviceBodyConfiguration,
	DeviceActionsConfiguration
}

/*
 *
 */
function DeviceBodyConfiguration(props) {
	const { device, options, onChange, elements = {} } = props;
	const classes = useStyles();
	
	const deviceStates = Object.keys(device.states || {});
	const [bodyStateKey,setBodyStateKey] = useState(options.bodyStateKey || null);
	
	/*
	useEffect(() => {
		setBodyStateKey(options.bodyStateKey || null);
	}, [options.bodyStateKey]);
	*/
	
	if (options.type !== 'device') {
		return <Inputs.Fieldset>
			<Inputs.FieldsetItem>
				{elements.Icon} {elements.Device}
			</Inputs.FieldsetItem>
		</Inputs.Fieldset>
	}
	
	// Action
	const bodyStateKeys = [''].concat(deviceStates.filter(stateKey => device.states[stateKey].action && device.states[stateKey].action !== ''));
	
	const bodyComponentsFunction = Object.keys((components[device.function] && components[device.function].body) || {});
	const bodyComponentsDefaults = Object.keys((components['_defaults'] && components['_defaults'].body) || {}).filter(item => item !== 'State' && bodyComponentsFunction.indexOf(item) < 0);
	const bodyComponents = [''].concat(bodyComponentsDefaults, bodyComponentsFunction);
	
	return (
		<Inputs.Fieldset key={'Device-' + (device.id || options.deviceId) + '-Body'}>
			<Inputs.FieldsetItem container>
				{elements.Icon} {elements.Device}
			</Inputs.FieldsetItem>
			
			<Inputs.FieldsetItem columns={12} noPadding>
				<Inputs.Text
					key='label'
					id='label'
					width={150}
					
					placeholder={device.name}
					val={options.label || ''}
					label='Device Label'
					onChange={onChange}
					/>
				
				<Inputs.Text
					key='bodyStateKey'
					id='bodyStateKey'
					width={140}
					
					autoComplete
					autoHighlight
					
					val={bodyStateKey}
					label='Body State Key'
					options={bodyStateKeys}
					onChange={(props, val) => {
						setBodyStateKey(val);
						onChange(props, val);
					}}
					/>
				
				{bodyStateKey !== null && <Inputs.Text
					classes={{ option: classes.wrap }}
					key='bodyElement'
					id='bodyElement'
					width={200}
					
					autoComplete
					autoHighlight
					
					val={options.bodyElement || null}
					label='Body Element'
					onChange={onChange}
					options={bodyComponents}
					renderOption={(option, state) => <Screenshot func={device.function} option={option} selected={state.selected} />}
					/>}
				
			</Inputs.FieldsetItem>
		</Inputs.Fieldset>
	)
}

/*
 *
 */
export function DeviceActionsConfiguration(props) {
	const { device, options, onChange } = props;
	const classes = useStyles();
	
	const deviceStates = Object.keys(device.states || {});
	const [checked,setChecked] = useState(options.actionType === 'action');
	const [actionStateKey,setActionStateKey] = useState(options.primaryStateKey || (deviceStates && deviceStates[0]) || null);
	
	if (options.type !== 'device') {
		return null;
	}
	
	// Checkbox Icon
	const icon = <CheckBoxOutlineBlankIcon fontSize="small" />
	const checkedIcon = <CheckBoxIcon fontSize="small" />
	
	// Action
	const actionStateKeys = [''].concat(deviceStates.filter(stateKey => device.states[stateKey].action && device.states[stateKey].action !== ''));
	
	const actionComponentsFunction = Object.keys((components[device.function] && components[device.function].action) || {});
	const actionComponentsDefaults = Object.keys((components['_defaults'] && components['_defaults'].action) || {}).filter(item => item !== 'State' && actionComponentsFunction.indexOf(item) < 0);
	const actionComponents = [''].concat(actionComponentsDefaults, actionComponentsFunction);
	
	return (
		<Inputs.Fieldset key={'Device-' + (device.id || options.deviceId) + '-Action'}>
			<Inputs.FieldsetItem>
				<Grid component="label" container alignItems="center">
					<Grid item>{i18n.t('Show states')}</Grid>
					<Grid item>
						<Switch
							color="default"
							size="small"
							checked={checked}
							onChange={(e, val) => {
								onChange({ id: 'actionType' }, val === true ? 'action' : 'state');
								
								// delete error of Action Element when switching to State
								if (val !== true) {
									onChange({ id: 'actionElement', error: null }, '');
								}
								
								// initial error of Action Element
								else {
									onChange({ id: 'actionElement', error: !options.actionElement }, '');
								}
								
								setChecked(prevChecked => prevChecked !== true);
							}}
							/>
							
					</Grid>
					<Grid item>{i18n.t('Show an action element')}</Grid>
				</Grid>
			</Inputs.FieldsetItem>
			<Inputs.FieldsetItem noPadding>
				
				{!checked && <>
				<Inputs.Text
					key='primaryStateKey'
					id='primaryStateKey'
					width={140}
					required
					
					autoComplete
					autoHighlight
					
					val={actionStateKey}
					label='Primary State Key'
					onChange={(props, val) => {
						setActionStateKey(val);
						onChange(props, val);
					}}
					options={[''].concat(deviceStates)}
					/>
					
				<Inputs.Text
					key='secondaryStatesKeys'
					id='secondaryStatesKeys'
					width={270}
					
					autoComplete
					autoHighlight
					multiple
					limitTags={2}
					disableCloseOnSelect
					size="small"
					
					val={options.secondaryStatesKeys || []}
					label='Secondary States Keys'
					onChange={onChange}
					options={deviceStates}
					
					renderOption={(option, { selected }) => (
						<React.Fragment>
							<Checkbox
								icon={icon}
								checkedIcon={checkedIcon}
								style={{ marginRight: 8 }}
								checked={selected}
								/>
							
							{option}
						</React.Fragment>
					)}
					/>
				</>}
				
				{checked && <>
				<Inputs.Text
					key='actionStateKey'
					id='primaryStateKey'
					width={140}
					required
					
					autoComplete
					autoHighlight
					
					val={actionStateKeys.indexOf(actionStateKey) > -1 ? actionStateKey : null}
					label='Action State Key'
					options={actionStateKeys}
					onChange={(props, val) => {
						setActionStateKey(val);
						onChange(props, val);
					}}
					/>
				
				{actionStateKey !== null && <Inputs.Text
					classes={{ option: classes.wrap }}
					key='actionElement'
					id='actionElement'
					width={200}
					required
					
					autoComplete
					autoHighlight
					
					val={options.actionElement || null}
					label='Action Element'
					onChange={onChange}
					options={actionComponents}
					renderOption={(option, state) => <Screenshot func={device.function} option={option} selected={state.selected} />}
					/>}
				</>}
				
			</Inputs.FieldsetItem>
		</Inputs.Fieldset>
	)
}

/**
 *
 */
function Screenshot(props) {
	const { func, selected } = props;
	const classes = useStyles();
	
	let option = props.option;
	
	// try to get screenshot of option
	if (option) {
		
		let screenshot = null;
		try {
			screenshot = require('../../functions/' + func + '/screenshots/' + option + '.png');
		}
		catch(err) {
			try {
				screenshot = require('../../functions/_defaults/screenshots/' + option + '.png');
			}
			catch(err) {}
		}
		
		if (screenshot) {
			option = <>
				<div>{option}</div>
				<img src={screenshot} alt="" />
			</>
		}
	}
	
	// empty option
	else {
		return option;
	}
	
	option = selected === true ? <strong>{option}</strong> : option;
	return <>
		{option}
		<Divider classes={{ root: classes.Divider }} />
	</>
}