import React from 'react'

import { DEFAULTS } from '../../../config/config'
import LayoutsConfig from './Layouts.config'

import i18n from '../../../controller/i18n'
import { moveKeyInArray } from '../../../helpers/utils'

import Grid from '@material-ui/core/Grid'
import IconButton from '@material-ui/core/IconButton'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'

import AddIcon from '@material-ui/icons/AddCircle'
import TabIcon from '@material-ui/icons/Tab'
import ArrowUpIcon from '@material-ui/icons/KeyboardArrowUp'
import ArrowDownIcon from '@material-ui/icons/KeyboardArrowDown'
import ArrowLeftIcon from '@material-ui/icons/KeyboardArrowLeft'
import ArrowRightIcon from '@material-ui/icons/KeyboardArrowRight'
import FullscreenIcon from '@material-ui/icons/Fullscreen'
import DeleteIcon from '@material-ui/icons/Delete'

import Inputs from '../../../components/Inputs'
import WidgetPage from './WidgetPage'


/*
 * STYLES
 */
import { withStyles } from '@material-ui/core/styles'
const styles = theme => ({
	deleteButton: {
		marginTop: '20px'
	},
	tabs: {
		marginBottom: '10px'
	},
	tab: {
		padding: theme.spacing(3, 2),
	},
	iconContainer: {
		flexDirection: 'column',
		alignItems: 'center'
	},
	columnContainer: {
		marginTop: theme.spacing(2),
	},
	column: {
		width: 250,
		margin: theme.spacing(1, 1),
		borderRight: '1px solid rgba(0, 0, 0, 0.12)',
		borderLeft: '1px solid rgba(0, 0, 0, 0.12)',
	},
	DialogActions: {
		justifyContent: 'initial'
	},
	alignRight: {
		display: 'flex',
		justifyContent: 'flex-end',
		
		'& > :not(:first-child)': {
			marginLeft: 8,
		}
	}
});


i18n.setTranslations(language => require('./i18n/' + language));

class LayoutsPage extends React.Component {
	
	constructor(props) {
		super(props);
		
		this.state = {
			selectedDrawer: null,
			selectedTab: null,
			selectedColumn: null,
			selectedWidget: null,
			
			layout: (Array.isArray(this.props.layout) && this.props.layout.length > 0 && this.props.layout) || [{ title: '', tabs: [] }],
			settings: this.props.settings
		}
	}
	
	/**
	 *
	 */
	_setLayout = (setter, callback) => {
		this.setState(setter, () => {
			callback && callback();
			this.props.onChange(this.state.layout, this.state.settings);
		});
	}
	
	/**
	 * 
	 */
	_saveWidget = widget => {
		const drawerIndex = this.state.selectedDrawer;
		const tabIndex = this.state.selectedTab;
		const columnIndex = this.state.selectedColumn;
		
		const widgetIndex = widget.index;
		delete widget.index;
		
		this._setLayout(prevState => {
			let nextLayout = [ ...prevState.layout || [] ];
			
			// fullscreen
			if (columnIndex === 'fullscreen') {
				nextLayout[drawerIndex].tabs[tabIndex]._fullscreen = widget;
			}
			
			// column
			else {
				if (widgetIndex >= 0) {
					nextLayout[drawerIndex].tabs[tabIndex].columns[columnIndex][widgetIndex] = widget;
				}
				else {
					nextLayout[drawerIndex].tabs[tabIndex].columns[columnIndex].push(widget);
				}
			}
			
			return {
				selectedWidget: null,
				layout: nextLayout
			}
		});
	}
	
	/**
	 *
	 */
	_addColumn = (drawerIndex, tabIndex, newColumn) => {
		
		this._setLayout(prevState => {
			let nextLayout = [ ...prevState.layout ];
			nextLayout[drawerIndex].tabs[tabIndex].columns.push([]);
			return { layout: nextLayout }
		});
	}
	
	/**
	 *
	 */
	_addTab = drawerIndex => {
		
		this._setLayout(prevState => {
			let nextLayout = [ ...prevState.layout || [] ];
			nextLayout[drawerIndex].tabs.push({
				title: i18n.t('add tab'),
				icon: 'home',
				columns: [
					[]
				]
			});
			
			return { layout: nextLayout }
			
		}, () => setTimeout(() => window.location = '#tab-' + (this.state.layout[drawerIndex].tabs.length-1), 100));
	}
	
	/**
	 *
	 */
	_changeTab = (drawerIndex, tabIndex) => ({ id }, val) => {
		id = id.substr(0, id.indexOf('-'));
		
		// edit tab icon / label
		if (id === 'icon' || id === 'title') {
			
			this._setLayout(prevState => {
				let nextLayout = [ ...prevState.layout || [] ];
				nextLayout[drawerIndex].tabs[tabIndex] = {
					...nextLayout[drawerIndex].tabs[tabIndex],
					[id]: val
				}
				
				return { layout: nextLayout }
			});
		}
		
		// edit tab fullscreen option
		else if (id === 'fullscreen') {
			
			this._setLayout(prevState => {
				let nextLayout = [ ...prevState.layout || [] ];
				
				if (nextLayout[drawerIndex].tabs[tabIndex]._fullscreen) {
					nextLayout[drawerIndex].tabs[tabIndex].columns = [
						[nextLayout[drawerIndex].tabs[tabIndex]._fullscreen]
					];
					
					delete nextLayout[drawerIndex].tabs[tabIndex]._fullscreen;
				}
				else {
					nextLayout[drawerIndex].tabs[tabIndex]._fullscreen = (nextLayout[drawerIndex].tabs[tabIndex].columns && nextLayout[drawerIndex].tabs[tabIndex].columns[0] && nextLayout[drawerIndex].tabs[tabIndex].columns[0][0]) || null;
					delete nextLayout[drawerIndex].tabs[tabIndex].columns;
				}
				
				return { layout: nextLayout }
			});
		}
	}
	
	/**
	 * 
	 */
	_deleteWidget = widgetIndex => {
		const drawerIndex = this.state.selectedDrawer;
		const tabIndex = this.state.selectedTab;
		const columnIndex = this.state.selectedColumn;
		
		this._setLayout(prevState => {
			let nextLayout = [ ...prevState.layout || [] ];
			nextLayout[drawerIndex].tabs[tabIndex].columns[columnIndex].splice(widgetIndex, 1);
			
			return {
				selectedWidget: null,
				layout: nextLayout
			}
		});
	}
	
	/**
	 * 
	 */
	_deleteColumn = (drawerIndex, tabIndex, columnIndex) => {
		
		this._setLayout(prevState => {
			let nextLayout = [ ...prevState.layout || [] ];
			nextLayout[drawerIndex].tabs[tabIndex].columns.splice(columnIndex, 1);
			return { layout: nextLayout }
		});
	}
	
	/**
	 *
	 */
	_deleteTab = (drawerIndex, tabIndex) => {
		
		this._setLayout(prevState => {
			let nextLayout = [ ...prevState.layout || [] ];
			nextLayout[drawerIndex].tabs.splice(tabIndex, 1);
			return { layout: nextLayout }
		});
	}
	
	/**
	 * 
	 */
	_moveWidget = (action, drawerIndex, tabIndex, columnIndex, widgetKey) => event => {
		event.stopPropagation();
		
		this._setLayout(prevState => {
			let nextLayout = [ ...prevState.layout || [] ];
			nextLayout[drawerIndex].tabs[tabIndex].columns[columnIndex] = moveKeyInArray([ ...nextLayout[drawerIndex].tabs[tabIndex].columns[columnIndex] ], widgetKey, action);
			return { layout: nextLayout }
		});
	}
	
	/**
	 * 
	 */
	_moveColumn = (action, drawerIndex, tabIndex, columnIndex) => {
		
		this._setLayout(prevState => {
			let nextLayout = [ ...prevState.layout || [] ];
			nextLayout[drawerIndex].tabs[tabIndex].columns = moveKeyInArray([ ...nextLayout[drawerIndex].tabs[tabIndex].columns ], columnIndex, action);
			return { layout: nextLayout }
		});
	}
	
	/**
	 *
	 */
	_moveTab = (action, drawerIndex, tabIndex) => event => {
		event.stopPropagation();
		
		this._setLayout(prevState => {
			let nextLayout = [ ...prevState.layout || [] ];
			nextLayout[drawerIndex].tabs = moveKeyInArray([ ...nextLayout[drawerIndex].tabs ], tabIndex, action);
			return { layout: nextLayout }
		});
	}
	
	/**
	 *
	 */
	_openPopup = (drawerIndex, tabIndex, columnIndex, widget) => {
		
		this.setState({
			selectedDrawer: drawerIndex,
			selectedTab: tabIndex,
			selectedColumn: columnIndex,
			selectedWidget: widget
		});
	}
	
	/*
	 *
	 */
	renderWidget(drawer, tab, column, widget) {
		const { classes } = this.props;
		
		return (

<ListItem
	key={'Tab-' + tab.index + '_Column-' + column.index + '_Widget-' + widget.index}
	onClick={() => this._openPopup(drawer.index, tab.index, column.index, widget)}
	button
	divider
	>
	
	{tab.fullscreen ?
	<ListItemIcon classes={{ root: classes.iconContainer }}>
		<FullscreenIcon />
	</ListItemIcon>
		:
	<ListItemIcon classes={{ root: classes.iconContainer }}>
		<React.Fragment>
			<IconButton size="small" onClick={this._moveWidget('up', drawer.index, tab.index, column.index, widget.index)}>
				<ArrowUpIcon />
			</IconButton>
			<IconButton size="small" onClick={this._moveWidget('down', drawer.index, tab.index, column.index, widget.index)}>
				<ArrowDownIcon />
			</IconButton>
		</React.Fragment>
	</ListItemIcon>}
	
	<ListItemText primary={
		<React.Fragment>
			<Typography display="block">{widget.title}</Typography>
			<Typography variant="subtitle2">({widget.module})</Typography>
		</React.Fragment>
	} />
	
</ListItem>

		);
	}
	
	/*
	 *
	 */
	renderColumn(drawer, tab, column) {
		const { classes } = this.props;
		
		// components per tab
		let tabColumnWidgetsNodes = [];
		tabColumnWidgetsNodes = column.widgets.map((widget, index) => {
			
			if (widget !== null) {
				widget.index = index;
				widget.module = widget.module || DEFAULTS.MODULE;
				return this.renderWidget(drawer, tab, column, widget);
			}
			
			return null;
		});
		
		return (

<Grid className={classes.column} key={'Tab-' + tab.index + '_Column-' + column.index} item>
	<List>
		{tab._fullscreen === undefined && <>
		<ListItem divider dense>
			<ListItemIcon classes={{ root: classes.iconContainer }}>
				<IconButton size="small" onClick={() => this._moveColumn('up', drawer.index, tab.index, column.index)}>
					<ArrowLeftIcon />
				</IconButton>
			</ListItemIcon>
			<ListItemText primary={i18n.t('move column')} />
			
			<ListItemSecondaryAction>
				<IconButton size="small" onClick={() => this._moveColumn('down', drawer.index, tab.index, column.index)}>
					<ArrowRightIcon />
				</IconButton>
			</ListItemSecondaryAction>
		</ListItem>
		<ListItem onClick={() => this._deleteColumn(drawer.index, tab.index, column.index)} button divider dense>
			<ListItemIcon classes={{ root: classes.iconContainer }}>
				<IconButton size="small">
					<DeleteIcon />
				</IconButton>
			</ListItemIcon>
			<ListItemText primary={i18n.t('delete column')} />
		</ListItem></>}
		
		{(tab._fullscreen === undefined || (tab._fullscreen !== undefined && column.widgets.length < 1))&& <ListItem onClick={() => this._openPopup(drawer.index, tab.index, column.index, { module: DEFAULTS.MODULE })} button divider dense>
			<ListItemIcon classes={{ root: classes.iconContainer }}>
				<IconButton size="small">
					<AddIcon />
				</IconButton>
			</ListItemIcon>
			<ListItemText primary={i18n.t('add widget')} />
		</ListItem>}
		
		{tabColumnWidgetsNodes}
		
	</List>
</Grid>

		);
	}
	
	/*
	 *
	 */
	renderTab(drawer, tab) {
		const { classes } = this.props;
		
		// columns per tab
		let columns = 1;
		let widgetsInFirstColumn = (tab.columns && tab.columns[1] && tab.columns[1].length) || 0;
		let tabColumnsNodes = [];
		
		// tab is fullscreen
		if (tab._fullscreen !== undefined) {
			tabColumnsNodes.push(this.renderColumn(drawer, tab, { widgets: tab._fullscreen ? [ tab._fullscreen ] : [], index: 'fullscreen' }));
		}
		
		// tab divided in columns
		else {
			columns = tab.columns.length;
			tabColumnsNodes = tab.columns.map((column, columnIndex) => this.renderColumn(drawer, tab, { widgets: column, index: columnIndex }));
		}
		
		return (

<ListItem
	key={'Tab-' + tab.id}
	id={'tab-' + tab.index}
	divider={tab.index !== (this.state.layout[drawer.index].tabs.length-1)}
	classes={{ root: classes.tab }}
	>
	
	<ListItemIcon key={'Drawer-' + drawer.id + '-Tab-' + tab.id + '-ListItemIcon'} classes={{ root: classes.iconContainer }}>
		<React.Fragment>
			<IconButton size="small" onClick={this._moveTab('up', drawer.index, tab.index)}>
				<ArrowUpIcon />
			</IconButton>
			<IconButton size="small" onClick={this._moveTab('down', drawer.index, tab.index)}>
				<ArrowDownIcon />
			</IconButton>
			<IconButton size="small" onClick={() => this._deleteTab(drawer.index, tab.index)} classes={{ root: classes.deleteButton }}>
				<DeleteIcon />
			</IconButton>
		</React.Fragment>
	</ListItemIcon>
	<ListItemText
		disableTypography={true}
		primary={[
			<Inputs.Text
				key={'Input-tabIcon-' + tab.id}
				id={'icon-' + tab.index}
				{...LayoutsConfig.tabIcon}
				
				onBlur={this._changeTab(drawer.index, tab.index)}
				val={tab.icon || ''}
				icon={tab.icon ? tab.icon.replace('mdi-', '') : ''}
				/>,
			
			<Inputs.Text
				key={'Input-tabTitle-' + tab.id}
				id={'title-' + tab.index}
				{...LayoutsConfig.tabTitle}
				
				onBlur={this._changeTab(drawer.index, tab.index)}
				val={tab.title || ''}
				/>,
			
			<Inputs.Button
				key={'Input-tabAddColumn-' + tab.id}
				id={'tabAddColumn-' + tab.index}
				label='add column'
				disabled={tab._fullscreen !== undefined}
				onClick={() => this._addColumn(drawer.index, tab.index, columns+1)}
				/>,
			
			<Inputs.Switch
				key={'Input-tabFullscreen-' + tab.id}
				id={'fullscreen-' + tab.index}
				{...LayoutsConfig.tabFullscreen}
				
				disabled={(columns > 1 || widgetsInFirstColumn > 1)}
				onChange={this._changeTab(drawer.index, tab.index)}
				val={tab._fullscreen !== undefined}
				/>,
			
			<Grid
				key={'Tab-' + tab.id + '-ColumnContainer'}
				classes={{ root: classes.columnContainer }}
				container
				>
				
				{tabColumnsNodes}
			</Grid>
		]}
		/>
	
</ListItem>

		);
	}
	
	/*
	 *
	 */
	renderDrawer(drawer) {
		const { classes } = this.props;
		
		const tabs = drawer.tabs.map((tab, tabIndex) => tab && this.renderTab(drawer, { ...tab, index: tabIndex, id: tab.title.toLowerCase().replace(/ /g, '') + tabIndex }));
		
		return (
			<List className={classes.tabs} key={'Drawer-' + drawer.id}>
				<ListItem divider classes={{ root: classes.tab }}>
					<ListItemIcon classes={{ root: classes.iconContainer }}>
						<TabIcon />
					</ListItemIcon>
					<ListItemText
						disableTypography={true}
						primary={[
							<Inputs.Button key='Input-addTabs' id='Input-addTabs' label='add tab' onClick={() => this._addTab(drawer.index)} />,
							<Inputs.Switch
								key='Input-tabsHideLabels'
								id='tabsHideLabels'
								{...LayoutsConfig.tabsHideLabels}
								
								onChange={({ id }, val) => this.props.onChange(this.state.layout, { [id]: val })}
								val={this.state.settings['tabsHideLabels']}
								/>
						]}
						/>
					
				</ListItem>
				{tabs}
				
			</List>
		)
	}
	
	/*
	 *
	 */
	render() {
		//const { classes } = this.props;
		
		const drawers = this.state.layout.map((drawer, drawerIndex) => drawer && this.renderDrawer({ ...drawer, index: drawerIndex, id: drawer.title.toLowerCase().replace(/ /g, '') + drawerIndex }));
		
		return (

<React.Fragment>
	<Paper>
		{drawers}
		
	</Paper>
	
	{this.state.selectedWidget !== null && <WidgetPage
												devices={this.props.devices}
												widget={this.state.selectedWidget}
												onSave={this._saveWidget}
												onDelete={this._deleteWidget}
												onClose={() => this.setState({ selectedWidget: null })}
												/>}
	
</React.Fragment>

		)
	}
}

export default withStyles(styles)(LayoutsPage);
