import Connection from '../../controller/Connection'
import Devices from '../../controller/Devices'
const clone = require('rfdc')()

export default 'Shelly'
export const namespace = 'shelly'

export const deviceObjectType = 'device';


/*
 * State Mapping
 */
const STATE_MAPPING = {

	// blinds
	"blind": {
		"level": {
			"state": ".Shutter.Position",
			"action": ".Shutter.Position"
		},
		"activity": {
			"state": ".Shutter.state",
			"action": ".Shutter.Pause"
		}
	},

	// lights
	"light": {
		"power": {
			"state": ".lights.Switch",
			"action": ".lights.Switch"
		},
		"level": {
			"state": ".lights.brightness",
			"action": ".lights.brightness"
		}
	}
}


/**
 *
 *
 * @param	{Object}	deviceStructure
 * @param	{String}	deviceStructure.root		root state
 * @param	{Array}		deviceStructure.states		all states
 * @param	{Object}	deviceStructure.values		all states with its value
 * @return	{Object}	device
 */
export function parse(deviceStructure, options) {
	const socket = Connection.getConnection;
	
	return new Promise(resolve => {
		
		socket.getObject(deviceStructure.root).then(obj => {
			
			let device = {
				'name': obj.common.name,
				'function': 'socket',
				'states': {
					'power': {
						'state': '.Relay0.Switch',
						'action': '.Relay0.Switch',
					},
					'firmware': {
						'state': '.firmware',
						'action': '.firmwareupdate'
					},
					'version': '.version',
					'reachability': '.online',
				}
			}
			
			// Shelly 1 or Shelly 1 PM
			if (deviceStructure.root.indexOf('SHSW-1') > -1 || deviceStructure.root.indexOf('SHSW-PM') > -1) {
				device.states = {
					...device.states,
					'Relay0': {
						'state': '.Relay0.Switch',
						'action': '.Relay0.Switch',
					}
				}
			}
			
			// Shelly 2
			else if (deviceStructure.root.indexOf('SHSW-2') > -1) {
				
				// Shutter mode
				if (deviceStructure.values[deviceStructure.root + '.mode'] && deviceStructure.values[deviceStructure.root + '.mode'].val === 'roller') {
					delete device.states.power;
					
					device.function = 'blind';
					device.states = {
						...clone(STATE_MAPPING['blind']),
						...device.states
					}
				}
				
				// Relay mode
				else {
					device.states = {
						...device.states,
						'Relay0': {
							'state': '.Relay0.Switch',
							'action': '.Relay0.Switch',
						},
						'Relay1': {
							'state': '.Relay1.Switch',
							'action': '.Relay1.Switch',
						},
					}
				}
			}
			
			// Shelly Dimmer
			else if (deviceStructure.root.indexOf('SHDM-1') > -1) {
				device.function = 'light';
				device.states = {
					...device.states,
					...clone(STATE_MAPPING['light'])
				}
			}
			
			// validate states
			device.states = Devices.validateStates(device.states, deviceStructure);
			
			// resolve
			resolve(device);
		});
	});
}