/**
 * Capitalize first letter of a word.
 *
 */
export default function ucFirst(str) {
	if (!str) {
		return '';
	}
	
	return str.charAt(0).toUpperCase() + str.slice(1);
}

/**
 * Move a key in an object up or down ("sort")
 *
 */
export function moveKeyInObject(obj, key, action) {
	
	// sort array
	let sorted = moveKeyInArray(Object.keys(obj), key, action);
	
	// build new object
	let next = {}
	sorted.map(key => next[key] = obj[key]);
	return next;
}

/**
 * Move a key in an object up or down ("sort")
 *
 */
export function moveKeyInArray(arr, keyOrIndex, action) {
	
	let index = typeof keyOrIndex === 'string' ? arr.indexOf(keyOrIndex) : keyOrIndex;
	let element = arr[index];
	
	// move up
	if (action === 'up' && index-1 >= 0) {
	  arr.splice(index-1, 0, element);
	  arr.splice(index+1, 1);
	}
	
	// move down
	else if (action === 'down' && index+1 < arr.length) {
	  arr.splice(index+2, 0, element);
	  arr.splice(index, 1);
	}
	
	return arr;
}

/**
 * Sort array or object by specific key
 *
 */
export function sort(arrayOrObject, key) {
	return Array.isArray(arrayOrObject) ? sortArray(arrayOrObject, key) : sortObject(arrayOrObject, key);
}

/**
 * Sort array by specific key
 *
 */
export function sortArray(arr, key) {
	
	arr = arr.slice(0);
	arr.sort((a, b) => {
		let x = (a[key] && a[key].toLowerCase()) || '';
		let y = (b[key] && b[key].toLowerCase()) || '';
		return x < y ? -1 : x > y ? 1 : 0;
	});
	
	return arr;
}

/**
 * Sort object by specific key
 *
 */
export function sortObject(obj, key) {
	
	return Object
		.entries(obj)
		.sort((a, b) => {
			if(a[1][key] < b[1][key]) { return -1; }
			if(a[1][key] > b[1][key]) { return 1; }
			return 0;
		})
		.reduce((_sortedObj, [k,v]) => ({
			..._sortedObj, 
			[k]: v
		}), {});
}

/**
 * 
 *
 */
export function joinConfig(UserConfig, AdapterConfig) {
	
	let config = {}
	config._defaults = {}
	
	AdapterConfig.forEach(param => {
		config._defaults[param.parameter] = param.val !== undefined ? param.val : param.placeholder;
		config[param.parameter] = UserConfig[param.parameter] !== undefined ? UserConfig[param.parameter] : (param.val !== undefined ? param.val : param.placeholder);
		
		if (param.json === true && config[param.parameter] !== undefined && typeof config[param.parameter] === 'string') {
			
			try {
				config[param.parameter] = JSON.parse(config[param.parameter]);
			}
			catch(err) {
				console.error(err);
			}
		}
	});
	
	return config;
}

/**
 * 
 * @see https://stackoverflow.com/a/39031032
 */
export function allSettled(promises) {
	let wrappedPromises = promises.map(p => Promise.resolve(p)
		.then(
			val => ({ status: 'fulfilled', value: val }),
			err => ({ status: 'rejected', reason: err })));
	
	return Promise.all(wrappedPromises);
}
