import React, { useState } from 'react'
import clsx from 'clsx'
import i18n from '../../controller/i18n'

import FormControl from '@material-ui/core/FormControl'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'

import Label from './Label'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles((theme) => ({
	Radio: {
		marginLeft: '2px',
	},
	RadioGroupFormControl: {
		height: '54px',
		marginTop: theme.spacing(1),
		border: '1px solid rgba(0, 0, 0, 0.23)',
		width: 'calc(100% - 10px)',
		borderRadius: '3px',
	},
	RadioGroup: {
		height: '100%',
	},
	RadioLabel: {
		fontFamily: '-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol" !important',
		margin: 0,
	},
}));


/*
 *
 */
export default function InputRadioGroup(props) {
	const { onChange, inputs, label, required, val } = props;
	const classes = useStyles();
	
	const [value,setValue] = useState(val);
	
	return (
		<FormControl className={classes.RadioGroupFormControl}>
			{label && <Label required={required} label={label} />}
			<RadioGroup
				className={classes.RadioGroup}
				row
				aria-label={label}
				name={i18n.t(label)}
				value={value}
				onChange={e => {
					setValue(e.target.value);
					onChange && onChange(props, e.target.value);
				}}
				>
				
				{inputs.map(input => <InputRadio key={input.label} {...input} />)}
			</RadioGroup>
		</FormControl>
	);
}

/*
 *
 */
export function InputRadio({ label, value, className }) {
	const classes = useStyles();
	return (
		<FormControlLabel
			classes={{ root: classes.RadioLabel, label: clsx(classes.RadioLabel, className) }}
			value={value}
			control={
				<Radio classes={{ root: classes.Radio }} />
			}
			label={i18n.t(label)} />
	);
}
