import React, { useState, useEffect } from 'react'
import clsx from 'clsx'
import color from 'color'

import Button from '@material-ui/core/Button'
import ButtonGroup from '@material-ui/core/ButtonGroup'
import ExpandLessIcon from '@material-ui/icons/ExpandLess'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import StopIcon from '@material-ui/icons/Stop'

import { allSettled } from '../../helpers/utils'
import Log from '../../controller/Log'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	value: props => ({
		float: 'left',
		lineHeight: '32px',
		marginRight: '10px'
	}),
	borderLeftTransparent: {
		borderLeftColor: 'transparent !important'
	},
	borderLeftColor: {
		borderLeftColor: color(theme.palette.primary.main).alpha(0.5).rgb().toString() +  '!important'
	},
	borderRightTransparent: {
		borderRightColor: 'transparent !important'
	},
	borderRightColor: {
		borderRightColor: color(theme.palette.primary.main).alpha(0.5).rgb().toString() +  '!important'
	},
	borderRightColorSecondary: {
		borderRightColor: color(theme.palette.secondary.main).alpha(0.5).rgb().toString() +  '!important'
	}
}));


/*
 * ACTIONS
 *
 */
function BlindLevelAction(props) {
	const { device, state, stateKey } = props;
	const classes = useStyles();
	
	// level & activity
	const [level, setLevel] = useState(state.value || 0);
	const [activity, setActivity] = useState(null);
	
	useEffect(() => {
		
		// state updates
		const stateChange = (sKey, s, d) => {
			sKey === 'level' && setLevel(s.val || 0);
			sKey === 'activity' && setActivity(s.val);
		}
		
		// listen for updates
		device.listen(stateChange);
		
		// request initial device states
		allSettled(device.requestDeviceStates(['level', 'activity'], true)).catch(res => Log._('Function (blind)', res.error.message, 'error', true, res.error.stack));
		
		// componentWillUnmount
		return () => device.unlisten(stateChange);
		
	}, [device]);
	
	const onGoUp = () => {
		setLevel(100);
		setActivity(true);
		device.setDeviceState(stateKey, 100).catch(res => Log._('Function (blind)', res.error.message, 'error', true, res.error.stack));
	}
	
	const onGoDown = () => {
		setLevel(0);
		setActivity(true);
		device.setDeviceState(stateKey, 0).catch(res => Log._('Function (blind)', res.error.message, 'error', true, res.error.stack));
	}
	
	const onStop = () => {
		setActivity(false);
		device.setDeviceState('activity', true).catch(res => Log._('Function (blind)', res.error.message, 'error', true, res.error.stack));
	}
	
	return (

<React.Fragment>
	
	<div className={classes.value}>
		{level + ' ' + state.unit}
	</div>
	
	<ButtonGroup size="small">
		<Button color="primary" className={clsx(activity !== null && (!activity ? classes.borderRightColor : classes.borderRightTransparent))} onClick={onGoUp}><ExpandLessIcon /></Button>
		{activity !== null && <Button color="secondary" className={clsx(!activity ? [ classes.borderLeftTransparent, classes.borderRightTransparent ] : classes.borderRightColorSecondary)} onClick={onStop} disabled={!activity}><StopIcon /></Button>}
		<Button color="primary" className={clsx(activity !== null && (!activity ? classes.borderLeftColor : classes.borderLeftTransparent))} onClick={onGoDown}><ExpandMoreIcon /></Button>
	</ButtonGroup>
</React.Fragment>

	);
}


export default {
	body: {
	},
	action: {
		BlindLevelAction
	}
}
