import React from 'react'

import defaults from '../_defaults/components'


/*
 * BODY
 *
 */
function LevelBody(props) {
	const Level = defaults.body.LevelBody;
	return (
		<Level min={0} max={35} {...props} />
	);
}


export default {
	body: {
		LevelBody
	},
	action: {
	}
}