import Socket from './Socket'
import { allSettled } from '../helpers/utils'

/**
 * Connection
 +
 *
 */
export default class Connection {

	/*
	 * Connection
	 */
	static connection = null;
	
	/**
	 * Discover connection.
	 *
	 * @param	{String}	url
	 * @param	{Array}		listeners
	 * @return	{Connection}
	 */
	static discover(urls) {
		
		let attempts = [];
		attempts = urls.map(url => new Promise((resolve, reject) => {
			
			// open socket
			let socket = new Socket(url);
			
			// connection successful
			socket.on('connect', () => {
				resolve({
					'socket': socket,
					'url': url
				});
			});
			
			// connection failed
			socket.on('error', err => {
				
				// close socket
				if (socket) {
					socket.disconnect();
					socket = null;
				}
				
				reject(err);
			});
		}));
		
		return new Promise((resolve, reject) => {
			allSettled(attempts).then(res => {
				res.forEach(attempt => {
					
					if (attempt.status === 'fulfilled') {
						Connection.connection = attempt.value.socket;
						return resolve(attempt.value);
					}
				});
				
				reject(urls);
			});
		});
	}
	
	/**
	 * Gets the current Connection.
	 *
	 * @param	void
	 * @return	{Connection}	Current Connection
	 */
	static get getConnection() {
		return Connection.connection || null;
	}
	
	/**
	 * Sets an event listener.
	 *
	 * @param	{Object}			listener
	 * @param	{String}			listener.event
	 * @param	{Function}			listener.callback
	 * @return	void
	 */
	static set setListener(listener) {
		Connection.connection.on(listener.event, listener.callback);
		return Connection;
	}
	
	/**
	 * Set event listeners.
	 *
	 * @param	{Array<Object>}		listeners
	 * @return 	{Boolean}
	 */
	static set setListeners(listeners = []) {
		
		if (!listeners || !Array.isArray(listeners)) {
			throw new Error('Incorrect parameter provided to setListeners');
		}
		
		listeners.forEach(listener => Connection.setListener = listener);
		return Connection;
	}
}
