import Connection from '../../controller/Connection'
import Devices from '../../controller/Devices'
const clone = require('rfdc')()

export default 'HomeMatic IP'
export const namespace = 'hm-rpc'

export const deviceObjectType = 'device';


/*
 * State Mapping
 */
const STATE_MAPPING = {

	// lights
	"light": {
		"HmIP-BRC2": {
			"power": {
				"state": ".3.STATE",
				"action": ".4.STATE"
			}
		},
		"HmIP-BDT": {
			"level": {
				"state": ".3.LEVEL",
				"action": ".4.LEVEL"
			}
		},
		"HM-LC-Sw1PBU-FM": {
			"power": {
				"state": ".1.STATE",
				"action": ".1.STATE"
			}
		}
	},

	// heatings
	"heating": {
		"HmIP-BWTH": {
			"temperature": {
				"state": ".1.ACTUAL_TEMPERATURE"
			},
			"setTemperature": {
				"state": ".1.SET_POINT_TEMPERATURE",
				"action": ".1.SET_POINT_TEMPERATURE"
			},
			"humidity": {
				"state": ".1.HUMIDITY"
			},
			"boost": {
				"state": ".1.BOOST_MODE",
				"action": ".1.BOOST_MODE"
			}
		},
		"HM-CC-RT-DN": {
			"temperature": {
				"state": ".4.ACTUAL_TEMPERATURE"
			},
			"setTemperature": {
				"state": ".4.SET_TEMPERATURE",
				"action": ".4.SET_TEMPERATURE"
			},
			"boost": {
				"state": ".4.BOOST_MODE",
				"action": ".4.BOOST_MODE"
			}
		}
	},

	// blinds
	"blind": {
		"HmIP-BROLL": {
			"level": {
				"state": ".3.LEVEL",
				"action": ".4.LEVEL"
			},
			"level2": {
				"state": ".3.LEVEL2",
				"action": ".4.LEVEL2"
			},
			"activity": {
				"state": ".3.ACTIVITY_STATE",
				"action": ".4.STOP"
			}
		},
		"HM-LC-Bl1-FM": {
			"level": {
				"state": ".1.LEVEL",
				"action": ".1.LEVEL"
			},
			"activity": {
				"state": ".1.WORKING",
				"action": ".1.STOP"
			}
		},
		"HM-LC-Bl1PBU-FM": {
			"level": {
				"state": ".1.LEVEL",
				"action": ".1.LEVEL"
			},
			"activity": {
				"state": ".1.WORKING",
				"action": ".1.STOP"
			}
		}
	}
}


/**
 *
 *
 * @param	{Object}	deviceStructure
 * @param	{String}	deviceStructure.root		root state
 * @param	{Array}		deviceStructure.states		all states
 * @param	{Object}	deviceStructure.values		all states with its value
 * @return	{Object}	device
 */
export function parse(deviceStructure, options) {
	const socket = Connection.getConnection;
	
	return new Promise(resolve => {
		
		socket.getObject(deviceStructure.root).then(obj => {
			
			let device = {
				'name': obj.common.name,
				'function': 'other',
				'states': {
					'unreach': '.0.UNREACH',
					'lowBattery': '.0.LOWBAT',
				}
			}
			
			switch(obj.native.TYPE.toLowerCase()) {
				
				// HomeMatic IP - HEATING
				case 'hmip-bwth': // HmIP-BWTH
					device.function = 'heating';
					device.states = {
						...device.states,
						...clone(STATE_MAPPING['heating']['HmIP-BWTH'])
					}
					break;
				
				// HomeMatic IP - WEATHER STATION
				case 'hmip-swo-b': 
					break;
				
				// HomeMatic IP - BLIND or JALOUSIE
				case 'hmip-broll': // HmIP-BROLL
					device.function = 'blind';
					device.states = {
						...device.states,
						...clone(STATE_MAPPING['blind']['HmIP-BROLL'])
					}
					break;
				
				// HomeMatic IP - SWITCH
				case 'hmip-brc2': // HmIP-BRC2
					device.function = 'light';
					device.states = {
						...device.states,
						...clone(STATE_MAPPING['light']['HmIP-BRC2'])
					}
					break;
				
				
				// HomeMatic IP - DIMMER
				case 'hmip-bdt': // HmIP-BDT
					device.function = 'light';
					device.states = {
						...device.states,
						...clone(STATE_MAPPING['light']['HmIP-BDT'])
					}
					break;
				
				
				// HomeMatic - WINDOW SENSOR
				case 'hm-sec-sco': // HM-Sec-SCo
				case 'hm-sec-sc-2': // HM-Sec-SC-2
					device.function = 'window';
					device.states = {
						'open': {
							'state': '.1.STATE',
						},
						...device.states
					}
					break;
				
				
				// HomeMatic - HEATING
				case 'hm-cc-rt-dn': // HM-CC-RT-DN
					device.function = 'heating';
					device.states = {
						...device.states,
						...clone(STATE_MAPPING['heating']['HM-CC-RT-DN'])
					}
					break;
				
				
				// HomeMatic - SWITCH
				case 'hm-lc-sw1pbu-fm': // HM-LC-Sw1PBU-FM
					device.function = 'light';
					device.states = {
						...device.states,
						...clone(STATE_MAPPING['light']['HM-LC-Sw1PBU-FM'])
					}
					break;
				
				
				// HomeMatic - BLIND
				case 'hm-lc-bl1pbu-fm': // HM-LC-Bl1PBU-FM
				case 'hm-lc-bl1-fm': // HM-LC-Bl1-FM
					device.function = 'blind';
					device.states = {
						...device.states,
						...clone(STATE_MAPPING['blind']['HM-LC-Bl1-FM']),
						...clone(STATE_MAPPING['blind']['HM-LC-Bl1PBU-FM'])
					}
					break;
				
				
				default:
					break;
			}
			
			// validate states
			device.states = Devices.validateStates(device.states, deviceStructure);
			
			// resolve
			resolve(device);
		});
	});
}