import React from 'react'
import i18n from '../../controller/i18n'

import FormLabel from '@material-ui/core/InputLabel'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles((theme) => ({
	Label: {
		backgroundColor: '#fafafa',
		padding: '0 6px',
		marginLeft: '-6px',
	},
}));


/*
 *
 */
export default function Label({ id, label, required, error }) {
	const classes = useStyles();
	
	return (
		<FormLabel
			classes={{ root: classes.Label }}
			required={required}
			shrink
			htmlFor={id}
			error={error}
			color="primary"
			variant="outlined"
			>
			
			{i18n.t(label)}
		</FormLabel>
	);
}