import React from 'react'

import { joinConfig } from '../../helpers/utils'


export default function iFrame(props) {
	let config = joinConfig(props.config, require('./iFrame.config.js').default);
	
	if (!config.url) {
		return null;
	}
	
	config.url = config.url.indexOf('http://') === -1 && config.url.indexOf('https://') === -1 ? 'http://' + config.url : config.url;
	
	return (
		<iframe style={{ border: 0 }} width="100%" height={config.height || '100%'} src={config.url} title={props.title} />
	);
}
