import Adapters from '../../config/adapters'

export default function getActivityType(device, stateKey, val) {
	let manufacturer = (device.attributes && device.attributes.manufacturer) || Adapters[device.states[stateKey].state.node.substr(0, device.states[stateKey].state.node.indexOf('.'))] || '';
	
	// HomeMatic
	if (manufacturer && manufacturer.namespace === 'HomeMatic') {
		
		switch(val) {
			case 1: // UP
			case 2: // DOWN
				val = true;
				break;
				
			case 3: // STABLE
				val = false;
				break;
				
			default: // UNKNOWN
				val = null;
				break;
		}
	}
	
	// Shelly
	else if (manufacturer && manufacturer.namespace === 'shelly') {
		
		switch(val) {
			case 'open': // MOVING
			case 'close': // MOVING
				val = true;
				break;
				
			case 'stop': // MOVING
				val = false;
				break;
				
			default: // UNKNOWN
				val = null;
				break;
		}
	}
	
	return val;
}