import i18n from '../../../controller/i18n'

export default {
	"pageSettings": {
		"label": "config#Settings#pageSettings#label",
		"settings": {
			"language": {
				"label": "config#Settings#pageSettings#settings#language#label",
				"type": "Select",
				"selection": [
					{ "de-DE": "Deutsch" },
					{ "en-GB": "English" }
				],
				"val": () => i18n.getLanguage(true)
			},
			"pageFavicon": {
				"label": "config#Settings#pageSettings#settings#pageFavicon#label",
				"info": "config#Settings#pageSettings#settings#pageFavicon#info",
				"type": "Text"
			},
			"pageTitle": {
				"label": "config#Settings#pageSettings#settings#pageTitle#label",
				"type": "Text",
				"placeholder": "jarvis - just another remarkable vis"
			}
		}
	},
	"themeSettings": {
		"label": "config#Settings#themeSettings#label",
		"settings": {
			"themeColorPrimary": {
				"label": "config#Settings#themeSettings#settings#themeColorPrimary#label",
				"info": "config#Settings#themeSettings#settings#themeColorPrimary#info",
				"link": "https://material-ui.com/customization/color/#color-palette",
				"type": "Text",
				"placeholder": "blue|700"
			},
			"themeColorSecondary": {
				"label": "config#Settings#themeSettings#settings#themeColorSecondary#label",
				"info": "config#Settings#themeSettings#settings#themeColorSecondary#info",
				"link": "https://material-ui.com/customization/color/#color-palette",
				"type": "Text",
				"placeholder": "pink|500"
			},
			"defaultBoxIcon": {
				"label": "config#Settings#themeSettings#settings#defaultBoxIcon#label",
				"type": "Text",
				"placeholder": "home"
			}
		}
	}
}