import React, { useState, useEffect, useRef, useCallback } from 'react'
import clsx from 'clsx'
import { v4 as uuid } from 'uuid'

import L from 'leaflet'
import { Map as LeafletMap, TileLayer, Marker as LeafletMarker, Tooltip as LeafletTooltip } from 'react-leaflet'

import Avatar from '@material-ui/core/Avatar'
import Box from '@material-ui/core/Box'
import Divider from '@material-ui/core/Divider'
import Tooltip from '@material-ui/core/Tooltip'
import Typography from '@material-ui/core/Typography'

import Devices from '../../controller/Devices'
import i18n from '../../controller/i18n'
import Settings from '../../controller/Settings'
import Log from '../../controller/Log'

import { joinConfig } from '../../helpers/utils'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	map: {
		width: '100%',
		height: '100%',
		position: 'relative'
	},
	mapOverview: {
		color: '#666',
		position: 'absolute',
		left: 0,
		bottom: 0,
		width: '100%',
		height: '60px',
		zIndex: 999,
		display: 'flex',
		alignItems: 'center',
		marginLeft: theme.spacing(2),
		
		'& > .AvatarContainer': {
			margin: theme.spacing(1),
			cursor: 'pointer',
		},
	},
	mapIconContainer: {
	},
	mapIconParent: style => ({
		backgroundColor: '#fff',
		boxShadow: '0 0 0 8px rgba(255, 255, 255, 0.3)',
		borderRadius: '50%',
		width: '100%',
		height: '100%',
		...style
	}),
	mapIcon: style => ({
		fontSize: '2rem',
		color: theme.palette.primary.main,
		transform: 'translate3d(4px, -4px,0)', /* for fontSize: '2.3rem' -> 'translate3d(2px, -8px,0)', */
		display: 'block',
		...style,
		backgroundColor: 'transparent',
		
		'&:after': {
			content: '""',
			display: 'block',
			padding: '50% 0%'
		}
	}),
	mapTooltip: {
	},
	Divider: {
		width: 2,
		height: '80%',
		backgroundColor: 'rgba(0, 0, 0,0.2)',
		margin: theme.spacing(0, 0.5, 1.5, 0.5),
	},
	AvatarContainer: {
	},
	Avatar: {
		margin: '0 auto'
	},
	AvatarName: {
		margin: 0,
		fontSize: '0.8rem',
		textAlign: 'center'
	}
}));


function getPosition(pos) {
	
	// json formatted
	if (pos.indexOf('{') > -1) {
		
		try {
			pos = JSON.parse(pos);
			pos = [ pos.lat || pos.latitude, pos.lon || pos.lng || pos.longitude ];
		}
		catch(err) {
			Log._('Modules (Map)', err.message, 'error', true, err.stack);
			pos = null;
		}
	}
	
	// string
	else if (pos) {
		pos = Array.isArray(pos) ? pos : pos.split(',');
	}
	
	return pos;
}

export default function Map(props) {
	const { devices } = props;
	const classes = useStyles();
	
	// get config
	let config = joinConfig(props.config, require('./Map.config.js').default);
	
	// get from settings
	const settingsLatitude = Settings.getSetting('iobroker').latitude;
	const settingsLongitude = Settings.getSetting('iobroker').longitude;
	
	// set states
	const [markers, setMarkers] = useState({});
	const [zoom,] = useState(config.defaultZoom);
	const [position,setPosition] = useState(getPosition(config.defaultPosition));
	
	useEffect(() => { // useEffect necessary because settings are retrieved async from backend
		!position && setPosition([ settingsLatitude, settingsLongitude ]);
	}, [ position, settingsLatitude, settingsLongitude ]);
	
	// map instance
	const mapId = 'map-' + uuid();
	const map = useRef();
	const memoizedMapInitialised = useCallback(function initMap() {
		map.current = this;
		
		// set height
		if (this._size.y === 0) {
			const mapElement = document.getElementById(mapId);
			
			if (mapElement) {
				mapElement.style.height = config.height + 'px';
				this.invalidateSize();
			}
		}
		
	}, [config, mapId]); // needs to be "function" for 'this' to be accessible
	
	// Marker click in overview
	const memoizedHandleClick = useCallback(flyTo => map.current.flyTo(Array.isArray(flyTo) ? flyTo : flyTo.split(',')), []);
	
	// get gps position coordinates
	useEffect(() => {
		
		// state change
		const stateChange = (stateKey, state, device) => {
			
			if (stateKey === 'position') {
				
				setMarkers(prevMarkers => {
					if (!prevMarkers[device.id] || prevMarkers[device.id].position !== state.val) {
						return { ...prevMarkers, [device.id]: device };
					}
					
					return prevMarkers;
				});
			}
		}
		
		// request device state and attach listener for every device
		Devices.forEachDevice(devices, (device, index, options) => {
			
			// device
			if (device._isDevice) {
				
				// listen for updates
				device.listen(stateChange, false);
				
				// request device state
				device.requestDeviceState('position').catch(err => {
					Log._('Modules (Map)', err.message, 'error', true, err.stack);
				});
			}
			
			// divider
			else {
				setMarkers(prevMarkers => ({ ...prevMarkers, [uuid()]: 'divider' }));
			}
		});
		
		// componentWillUnmount
		return () => Devices.forEachDevice(devices, (device, index, options) => device._isDevice && device.unlisten(stateChange));
		
	}, [devices]);
	
	
	// add markers
	let mapMarkers = [], mapOverview = [];
	if (devices.length > 0 && devices.length === Object.keys(markers).length) {
		
		for (let key in markers) {
			const device = markers[key];
			
			//
			if (device === 'divider') {
				mapOverview.push(<Divider key={key} orientation="vertical" classes={{ root: classes.Divider }} />);
			}
			
			//
			else if (device !== null && device.states.position && device.states.position.state && device.states.position.state.value && device.states.position.state.value.val) {
				const markerPosition = getPosition(device.states.position.state.value.val);
				mapMarkers.push(<Marker key={'marker-' + device.id} position={markerPosition} device={device} config={config} />);
				
				let item = null;
				if (device.states.position.icon && device.states.position.icon.indexOf('data:image') > -1) {
					item = <Avatar key={'overview-' + device.id} alt={device.name} src={device.states.position.icon} onClick={() => memoizedHandleClick(markerPosition)} />
				}
				else {
					const icon = device.getIcon('position', null, 'map-marker');
					const styles = device.getStyle('icon', 'position');
					
					item = <Avatar classes={{ root: classes.Avatar }} style={styles} key={'overview-' + device.id} alt={device.name} onClick={() => memoizedHandleClick(markerPosition)}><span className={clsx('mdi mdi-' + icon.replace('mdi-', ''))}></span></Avatar>
				}
				
				mapOverview.push(
					<Tooltip key={'tooltip-' + device.id} title={device.name} aria-label={device.name} arrow>
						<div className={clsx('AvatarContainer', classes.AvatarContainer)}>
							{item}
							<p className={classes.AvatarName}>{device.name}</p>
						</div>
					</Tooltip>
				);
			}
		}
	}
	
	return (

<div id={mapId} className={classes.map}>
	<Box className={classes.mapOverview}>
		<Typography>{i18n.t('Jump to')}:</Typography>
		{mapOverview}
	</Box>
	<LeafletMap useFlyTo={true} center={position || [53.5505, 9.9925]} zoom={zoom} className={classes.map} whenReady={memoizedMapInitialised}>
		<TileLayer
			attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
			url='https://{s}.tile.osm.org/{z}/{x}/{y}.png'
			/>
		
		{mapMarkers}
	</LeafletMap>
</div>

	);
}


function Marker(props) {
	const { position, device, config } = props;
	const classes = useStyles(device.getStyle('icon', 'position', null));
	
	let markerIcon = null, offset = 40;
	//Log._('Modules (Map)', 'Marker for ' + device.function + ' ' + device.name + ' added to map.');
	
	// Marker with avatar / picture
	if (device.states.position.icon && device.states.position.icon.indexOf('data:image') > -1) {
		markerIcon = L.divIcon({
			className: classes.mapIconContainer,
			html: '<img src="' + device.states.position.icon + '" class="' + classes.mapIconParent + '" />',
			iconSize: [ 64, 64 ]
		});
	}
	
	// Marker with Icon
	else {
		offset = 30;
		const icon = device.getIcon('position', null, 'map-marker');
		
		markerIcon = L.divIcon({
			className: classes.mapIconContainer,
			html: '<div class="' + clsx(classes.mapIconParent) + '" style=""><span class="' + clsx(classes.mapIcon, 'mdi mdi-' + icon.replace('mdi-', '')) + '"></span></div>',
			iconSize: [ 40, 40 ]
		});
	}
	
	// Marker MouseOver / MouseOut (bring tooltip to front if markers overlay)
	const memoizedHandleMouseEvent = useCallback(function() {this.closeTooltip().openTooltip()}, [],);

	return (

<LeafletMarker riseOnHover={true} position={Array.isArray(position) ? position : position.split(',')} icon={markerIcon} onMouseOver={memoizedHandleMouseEvent}>
	<LeafletTooltip className={classes.mapTooltip} direction={config.tooltipDirection} permanent={config.tooltipPermanent} offset={[0,offset]}>
		{device.name}
	</LeafletTooltip>
</LeafletMarker>

	);
}
