
export default {
	"on": {
		"body": null,
		"action": "SwitchAction",
		
		"icon": {
			"true": "lightbulb-on",
			"false": "lightbulb-off-outline"
		}
	},
	"power": { // SAME AS ON
		"body": null,
		"action": "SwitchAction",
		
		"icon": {
			"true": "lightbulb-on",
			"false": "lightbulb-off-outline"
		}
	},
	"dimmer": {
		"body": "LevelBody",
		"action": null,
		
		"value": val => val > 0 && val <= 1 ? val * 100 : val,
		"unit": val => val > 0 ? " %" : null,
		"stateStyle": {
			"0": {
				"color": "#999"
			}
		},
		"icon": {
			"default": "lightbulb-on",
			"0": "lightbulb-off-outline"
		},
		"display": {
			"0": "light#level#off"
		}
	},
	"level": { // SAME AS DIMMER
		"body": "LevelBody",
		"action": null,
		
		"value": val => val > 0 && val <= 1 ? val * 100 : val,
		"unit": val => val > 0 ? " %" : null,
		"stateStyle": {
			"0": {
				"color": "#999"
			}
		},
		"icon": {
			"default": "lightbulb-on",
			"0": "lightbulb-off-outline"
		},
		"display": {
			"0": "light#level#off"
		}
	},
	"ct": {
		"body": "LightTemperatureBody",
		"action": null,
		
		"icon": {
			"default": "thermometer"
		},
		"unit": " °K"
	},
	"colortemperature": {
		"body": "LightTemperatureBody",
		"action": null,
		
		"icon": {
			"default": "thermometer"
		},
		"unit": " °K"
	},
	"hue": {
		"body": "LightHueBody",
		"action": "LightColorState",
		
		"icon": {
			"default": "palette"
		},
		"unit": " °"
	},
	"rgb": {
		"body": "LightColorBody",
		"action": "LightColorState",
		
		"icon": {
			"default": "palette"
		}
	},
	"hsv": {
		"body": "LightColorBody",
		"action": "LightColorState",
		
		"icon": {
			"default": "palette"
		}
	},
	"hex": {
		"body": "LightColorBody",
		"action": "LightColorState",
		
		"icon": {
			"default": "palette"
		}
	}
}