import React from 'react'
import clsx from 'clsx'

import { DEFAULTS } from './config/config'

import Connection from './controller/Connection'
import Devices from './controller/Devices'
import i18n from './controller/i18n'
import Log from './controller/Log'
import Settings from './controller/Settings'

import Divider from '@material-ui/core/Divider'
import Hidden from '@material-ui/core/Hidden'
import { withTheme } from '@material-ui/core/styles'

//import { SwipeableViews } from '@tarragon/swipeable-tabs'

import TopBar from './components/TopBar'
import TabBar, { TabPanel } from './components/TabBar'
//import Notifications from './components/Notifications'
import GridContainer from './components/GridContainer'
import DeviceStateDetails from './components/DeviceStateDetails'
import Widget from './components/Widget'

import Modules, { ModuleSettings } from './modules'
import Pages from './pages'


/**
 *
 *
 */
const Module = React.memo(props => {
	Log._('Jarvis', [ 'RENDER Module', props ], 'development');
	const { widget, actions = {} } = props;
	
	// widget has no devices assigned
	/*
	if (ModuleSettings[widget.module] && ModuleSettings[widget.module].noDevicesAllowed !== true) {
		Log._('Jarvis', 'Widget ' + (widget.title || widget.module) + ' has no devices assigned to it!', 'error');
		return null;
	}
	*/
	
	// get module
	const Tag = Modules[widget.module || DEFAULTS.MODULE];
	
	// get tag
	if (Tag === undefined) {
		Log._('Jarvis', 'Undefined module "' + widget.module + '" used!', 'error');
		return null;
	}
	
	return <Tag
				key={'tag-' + widget.module + '-' + widget.title}
				devices={widget.devices || []}
				openDialog={actions.showDevice ? actions.showDevice : null}
				config={widget.moduleConfig || {}}
				/>
	
}, (prevProps, nextProps) => {
	return !prevProps.devices || !nextProps.devices || prevProps.devices.length === nextProps.devices.length;
});

/**
 *
 *
 */
const Widgets = React.memo(props => {
	Log._('Jarvis', [ 'RENDER Widgets', props ], 'development');
	const { id, widgets } = props;
	
	return widgets.map((widget, i) => {
		
		if (!widget) {
			return null;
		}
		
		widget.module = widget.module || DEFAULTS.MODULE;
		const moduleSettings = ModuleSettings[widget.module] || {};
		
		return (
			<Widget
				key={id + '-widget-' + i}
				title={widget.title === null ? null : (widget.title || i18n.t(moduleSettings.title))}
				icon={widget.icon === null ? null : (widget.icon || moduleSettings.icon)}
				iconStyle={widget.iconStyle}
				>
				
				<Module {...props} widget={widget} />
			</Widget>
		);
	}).concat([
        <Hidden key={id + '-divider'} smUp>
			<Divider style={{ margin: '8.2px 20px 4px 20px', height: 0 }}/>
        </Hidden>
	]);
	
}, (prevProps, nextProps) => {
	return prevProps.widgets.length === nextProps.widgets.length;
});

/**
 *
 *
 */
const TabPanels = React.memo(props => {
	Log._('Jarvis', [ 'RENDER TabPanels', props ], 'development');
	const { tabs, selectedTab } = props;
	
	return tabs.map((tab, tabIndex) => {
		
		// detect fullscreen
		const fullscreen = tab._fullscreen !== undefined;
		
		// get widgets for each column
		let tabGridColumnsContent = null;
		if (fullscreen) {
			tabGridColumnsContent = tab._fullscreen && <Module {...props} widget={tab._fullscreen} />
		}
		else if (tab.columns && Array.isArray(tab.columns)) {
			let tabGridColumnsContents = {};
			
			tab.columns.forEach((column, columnIndex) => {
				tabGridColumnsContents[columnIndex+1] = <Widgets {...props} id={'tab-' + tabIndex + '-column-' + columnIndex} widgets={column} />
			});
			
			tabGridColumnsContent = <GridContainer contents={tabGridColumnsContents} />
		}
		
		// render tab
		return (
			<TabPanel
				key={'tabPanel-' + tabIndex}
				renderedTab={tabIndex}
				selectedTab={selectedTab}
				fullscreen={fullscreen}
				>
				
				{tabGridColumnsContent}
			</TabPanel>
		);
	});
	
}, (prevProps, nextProps) => {
	//console.log(prevProps, nextProps);
	return prevProps.selectedTab === nextProps.selectedTab;
});


/**
 *
 *
 */
class Jarvis extends React.Component {
	
    constructor(props) {
		super(props);
		
		this.state = {
			selectedTab: 0,
			tabTitle: '',
			
			page: 'home',
			
			notificationDrawer: false,
			notifications: [],
			notificationsUnread: 0,
			
			dialog: false,
			dialogContents: {
				'ts': 0,
				'device': {}
			}
		}
		
		// bind methods
		this.closeDialog = this.closeDialog.bind(this);
		this.openDialog = this.openDialog.bind(this);
		this.onSelectTab = this.onSelectTab.bind(this);
		this.toggleDrawer = this.toggleDrawer.bind(this);
		this.navigate = this.navigate.bind(this);
		
		// get connection
		this.socket = Connection.getConnection;
		
		// initialise
		this.devices = Devices.getDevices();
		this.tabs = (Settings.getLayout()[0] && Settings.getLayout()[0].tabs) || [];
		this.tabsEnabled = this.tabs && this.tabs.length > 1;
		
		this.state.tabIcon = this.tabs[this.state.selectedTab] && this.tabs[this.state.selectedTab].icon;
		this.state.tabTitle = this.tabs[this.state.selectedTab] && this.tabs[this.state.selectedTab].title;
	}
	
	componentDidMount() {
		Log._('Jarvis', 'componentDidMount', 'development');
		
		// check if devices and settings are defined
		if (!this.devices || Object.keys(this.devices).length === 0 || !this.tabs || this.tabs.length === 0) {
			this.navigate('configuration');
		}
		
		// get notifications
		/*
		this.socket.getState('jarvis.0.notifications').then(notifications => {
			notifications = (notifications && notifications.val && JSON.parse(notifications.val)) || [];
			
			this.setState({
				notificationsUnread: notifications.filter(notification => !notification.unread || notification.unread !== false).length,
				notifications: notifications
			});
		});
		
		this.socket.subscribeState(DEFAULTS.NODE_NOTIFICATIONS, notifications => Log._('Jarvis', notifications));
		*/
	}
	
	closeDialog() {
		this.setState({ 'dialog': false, 'dialogContents': { 'ts': Date.now(), 'device': {} } });
	}
	
	openDialog(device) {
		this.setState({ 'dialog': true, 'dialogContents': { 'ts': Date.now(), 'device': device } });
	}
	
	onSelectTab(tab) {
		this.setState({
			selectedTab: tab,
			tabIcon: this.tabs[tab].icon,
			tabTitle: this.tabs[tab].title
		});
	}
	
	toggleDrawer(drawer, open) {
		this.setState({ [drawer]: open });
	}
	
	navigate(page) {
		this.setState({ page });
	}
	
    render() {
		Log._('Jarvis', 'RENDER render()', 'development');
		
		const Page = Pages[this.state.page] || null;
		
		const tabPanels = <TabPanels page={this.state.page} tabs={this.tabs} selectedTab={this.state.selectedTab} actions={{ showDevice: this.openDialog }} />
        return (

<React.Fragment>
	<TopBar
		title={
			(this.tabsEnabled && <>
				{this.state.tabIcon && <span className={clsx(this.props.theme.components.tabIcon, 'mdi mdi-' + this.state.tabIcon)}></span>}
				{this.state.tabTitle !== 'null' && <span> {this.state.tabTitle}</span>}
			</>) ||
			Settings.getSetting('topBarTitle') ||
			Settings.getSetting('pageTitle') ||
			'jarvis - Smart Home Overview'
		}
		
		connectionStatus={this.props.connectionStatus}
		toggleDrawer={this.toggleDrawer}
		navigate={this.navigate}
		//notifications={this.state.notificationsUnread}
		>
		
		{this.tabsEnabled && <TabBar tabs={this.tabs} selectedTab={this.state.selectedTab} onSelectTab={this.onSelectTab} />}
	</TopBar>
	
	
	{/* DRAWER: NOTIFICATIONS 
	<Notifications
		open={this.state.notificationDrawer}
		toggleDrawer={this.toggleDrawer}
		notifications={this.state.notifications}
		/>*/}
	
	
	{/* PAGE */}
	{Page && <Page
		page={this.state.page}
		navigate={this.navigate}
		devices={this.devices}
		/>}
	
	
	{/* POPUP */}
	<DeviceStateDetails
		open={this.state.dialog}
		contents={this.state.dialogContents}
		closeDialog={this.closeDialog}
		/>
	
	
	{/* TABS */}
{/*
	<Hidden smUp>
	<SwipeableViews
		views={tabPanels}
		selectedView={this.state.selectedTab}
		onSwipe={this.onSelectTab}
		/>
	</Hidden>
	<Hidden smDown>
		{tabPanels}
	</Hidden>
*/}
	{tabPanels}
	
</React.Fragment>

		)
	}
}

export default withTheme(Jarvis);