import Connection from './Connection'
import Function from './Function'
import i18n from './i18n'
import Log from './Log'

import { formatDistanceToNow } from 'date-fns'


/**
 * Device
 *
 * @param	{Object}	deviceProperties
 * @param	{String}	deviceProperties.id
 * @param	{String}	deviceProperties.name
 * @param	{String}	[deviceProperties.room=null]
 * @param	{String}	deviceProperties.function
 * @param	{Object}	deviceProperties.states
 * @param	{Object}	[deviceProperties.attributes={}]
 * @param	{Object}	deviceProperties.options
 * @param	{Object}	socket
 * @return	{Device}
 * @see https://github.com/Zefau/ioBroker.jarvis/issues/21
 *
 */
export default class Device extends Function {
	
	constructor(deviceProperties) {
		const socket = Connection.getConnection
		
		try {
			super(deviceProperties.function);
		}
		catch(err) {
			throw new Error('No function given for device ' + deviceProperties.name + '!');
		}
		
		// initialise class data
		this._isDevice = true;
		this.id = deviceProperties.id;
		this.name = deviceProperties.name;
		
		this.room = deviceProperties.room || null;
		
		this.states = deviceProperties.states;
		this.options = deviceProperties.options;
		this.attributes = deviceProperties.attributes || {};
		
		this.socket = socket;
		
		// verify data
		if (!this.states || typeof this.states !== 'object' || Object.keys(this.states).length === 0) {
			throw new Error('Insufficent properties provided' + (deviceProperties.id || deviceProperties.name ? ' for device ' + (deviceProperties.name || deviceProperties.id) : '') + ' (no states defined)!');
		}
		else if (!this.id || !this.name || !this.function) {
			throw new Error('Insufficent properties provided' + (deviceProperties.id || deviceProperties.name ? ' for device ' + (deviceProperties.name || deviceProperties.id) : '') + ' (no id, name or function defined)!');
		}
		else if (!this.socket) {	
			throw new Error('No socket given!');
		}
		
		// hierarchy
		this.children = deviceProperties.children || [];
		this.parent = deviceProperties.parent || null;
		
		// get states
		this._setStatesOptions();
		this.stateSubscriptions = [];
		
		// listen for errors of the device
		this.on('error', err => Log._('Device', [ i18n.t('Device error occured!'), err.message ], 'error', false, err.stack));
	}
	
	/**
	 *
	 *
	 * @return	{Object|String}
	 * @private
	 */
	_getStateFromKey(stateKey = null, stateType = 'state') {
		
		// already a state object
		if (stateKey && typeof stateKey === 'object') {
			return stateKey;
		}
		
		// get state object
		let s = this.states[stateKey || this.primaryStateKey];
		
		// state does not exist
		if (s === undefined) {
			return i18n.t('State %stateKey does not exist in device %device').replace(/%stateKey/g, stateKey).replace(/%device/g, this.name) + '!';
		}
		
		// state not an object so far
		else if (typeof s === 'string') {
			this.states[stateKey || this.primaryStateKey] = { [stateType]: { 'node': s }};
			s = this.states[stateKey || this.primaryStateKey];
		}
		else if (typeof s[stateType] === 'string') {
			this.states[stateKey || this.primaryStateKey][stateType] = { 'node': s[stateType] };
			s = this.states[stateKey || this.primaryStateKey];
		}
		
		// state not valid
		if (!s || !s[stateType]) {
			return i18n.t('Incorrect or non-defined state key %stateKey for device %device').replace(/%stateKey/g, stateKey).replace(/%device/g, this.name).replace(/%stateType/g, stateType) + '!';
		}
		else if (typeof s[stateType] == 'object' && !s[stateType].node) {
			return i18n.t('No %stateType specified for device %device').replace(/%device/g, this.name).replace(/%stateType/g, stateType) + '!';
		}
		
		return s;
	}
	
	/**
	 *
	 *
	 * @private
	 */
	_getConfigurationSetting(config, stateKey, attribute, stateVal) {
		//console.log(this.id, config, stateKey, attribute, stateVal, this[config], this[config][stateKey], this[config]['_any']);
		
		if (this[config][stateKey] && this[config][stateKey][attribute]) {
			
			// 1. if stateKey (e.g. level, power) is available, try to replace an attribute (e.g. icon, unit or for styles state, icon) based on current state (e.g. true, false)
			if (typeof this[config][stateKey][attribute] === 'object') {
				
				const stateValKeys = Object.keys(this[config][stateKey][attribute]);
				if (stateValKeys.toString().indexOf('<') > -1 || stateValKeys.toString().indexOf('>') > -1 || stateValKeys.toString().indexOf('!=') > -1 || stateValKeys.toString().indexOf('=') > -1) { // =, !=, >, >=, <, <=
					
					let val = this[config][stateKey][attribute]['default'];
					stateValKeys.forEach(stateValKey => {
						
						const stateValValue = stateValKey.replace('<=', '').replace('>=', '').replace('<', '').replace('>', '').replace('!=', '').replace('=', '');
						const operator = stateValKey.replace(stateValValue, '');
						
						if (operator === '<=' && stateVal <= stateValValue) {
							val = this[config][stateKey][attribute][stateValKey];
						}
						else if (operator === '<' && stateVal < stateValValue) {
							val = this[config][stateKey][attribute][stateValKey];
						}
						else if (operator === '>=' && stateVal >= stateValValue) {
							val = this[config][stateKey][attribute][stateValKey];
						}
						else if (operator === '>' && stateVal > stateValValue) {
							val = this[config][stateKey][attribute][stateValKey];
						}
						else if (operator === '!=' && stateVal != stateValValue) {  // eslint-disable-line eqeqeq
							val = this[config][stateKey][attribute][stateValKey];
						}
						else if ((operator === '' || operator === '=') && stateVal == stateValValue) {  // eslint-disable-line eqeqeq
							val = this[config][stateKey][attribute][stateValKey];
						}
					});
					
					return val;
				}
				else {
					return super._isValue(this[config][stateKey][attribute][stateVal]) ? this[config][stateKey][attribute][stateVal] : this[config][stateKey][attribute]['default'];
				}
			}
			
			// 2. if #1 is a function, apply it
			else if (super._isFunction(this[config][stateKey][attribute])) {
				return this[config][stateKey][attribute](stateVal, stateKey, this);
			}
			
			// 3. fallback to stateKey with attribute, if defined but neither an object (thus stateless) nor a function
			else if (typeof this[config][stateKey][attribute] === 'string') {
				return this[config][stateKey][attribute];
			}
		}
		else if (this[config]['_any'] && this[config]['_any'][attribute]) {
			
			// 4. fallback to _any if no stateKey (e.g. level, power) is given, but consider attribute to be stateful (e.g. true, false)
			if (typeof this[config]['_any'][attribute] === 'object') {
				return super._isValue(this[config]['_any'][attribute][stateVal]) ? this[config]['_any'][attribute][stateVal] : this[config]['_any'][attribute]['default'];
			}
			
			// 5. if #4 is a function, apply it
			else if (super._isFunction(this[config]['_any'][attribute])) {
				return this[config]['_any'][attribute](stateVal, stateKey, this);
			}
		}
		
		// nothing, return undefined (not null, because this would mean take an empty value)
		return undefined;
	}
	
	/**
	 *
	 *
	 * @private
	 */
	_getUserSetting(stateKey, attribute, stateVal) {
		return this._getConfigurationSetting('states', stateKey, attribute, stateVal);
	}
	
	/**
	 *
	 *
	 *
	 * @return void
	 * @private
	 *
	 */
	_setStatesOptions() {
		
		let states = Object.keys(this.states);
		
		// delete ignored states
		if (this.options.ignoredStates) {
			this.options.ignoredStates = Array.isArray(this.options.ignoredStates) ? this.options.ignoredStates : [this.options.ignoredStates];
			
			this.options.ignoredStates.forEach(ignoredState => {
				if (this.states[ignoredState]) {
					delete this.states[ignoredState];
				}
			});
		}
		
		// get hidden states
		this.options.hiddenStates = this.options.hiddenStates ? (Array.isArray(this.options.hiddenStates) ? this.options.hiddenStates : [this.options.hiddenStates]) : [];
		
		// primary and secondary state
		this.primaryStateKey = this.options.primary || states[0] || null;
		//this.states[this.primaryStateKey] = this._getStateFromKey(this.primaryStateKey);
	}
	
	/**
	 *
	 *
	 *
	 * @return	{Object}
	 * @private
	 */
	_updateDeviceState(stateKey, state) {
		
		// add time changed
		state.timeChanged = formatDistanceToNow(new Date(state.lc-5*1000), { locale: i18n.getLocale(), includeSeconds: true, addSuffix: true })
		
		// add unit
		state.unit = '';
		
		// change val
		let val = this._getConfigurationSetting('configurations', stateKey.toLowerCase(), 'val', state.val);
		state.val = val !== undefined ? val : state.val;
		
		// assign value
		state.value = state.val;
		
		// mapping of value to state values
		[{ 'value': 'value' }, { 'display': 'value' }, { 'unit': 'unit' }].forEach(pair => {
			let attribute = Object.keys(pair).toString();
			let option = Object.values(pair).toString();
			let settings = {};
			
			// get user setting
			settings['options'] = state.val !== null && this._getUserSetting(stateKey, attribute, state.val);
			
			// get function setting or fallback to defaults
			['configurations', 'defaults'].forEach(config => {
				settings[config] = state.val !== null && this._getConfigurationSetting(config, stateKey.toLowerCase(), attribute, state.val);
			});
			
			state[option] = super._getValueFromSettings(settings, state[option]);
		});
		
		return state;
	}
	
	/**
	 *
	 *
	 */
	getDeviceStates() {
		
		let devicesStates = {};
		for (let stateKey in this.states) {
			devicesStates[stateKey] = this.getDeviceState(stateKey);
		}
		
		return devicesStates;
	}
	
	/**
	 *
	 *
	 * @param	{String}	[stateKey=this.primaryStateKey]
	 * @param	{String}	[stateProperty=null]
	 * @return	{String}
	 */
	getDeviceState(stateKey = null, stateProperty = null) {
		const s = this.states[stateKey || this.primaryStateKey];
		
		let deviceState = null;
		
		if (s && s.state && stateProperty !== null && s.state.value !== undefined && s.state.value[stateProperty] !== undefined) {
			deviceState = s.state.value[stateProperty];
		}
		else if (s && s.state && stateProperty === null && s.state.value !== undefined) {
			deviceState = s.state.value;
		}
		
		return deviceState;
	}
	
	/**
	 * Get an icon based on the current device state (or falls back to a default).
	 *
	 * @param	{String}	[stateKey=this.primaryStateKey]
	 * @param	{String}	[stateVal=default]
	 * @param	{String}	[defaultIcon=null]
	 * @return	{String}
	 */
	getIcon(stateKey, stateVal = '', defaultIcon = undefined) {
		
		let settings = {};
		stateKey = stateKey || this.primaryStateKey;
		
		// get user setting
		settings['options'] = this._getUserSetting(stateKey, 'icon', stateVal);
		
		// get function setting or fallback to defaults
		['configurations', 'defaults'].forEach(config => {
			settings[config] = this._getConfigurationSetting(config, stateKey.toLowerCase(), 'icon', stateVal);
		});
		
		return super._getValueFromSettings(settings, defaultIcon);
	}
	
	/**
	 *
	 *
	 */
	getOption(option, stateVal = 'default') {
		return (this.options[option] && (this.options[option][stateVal] || this.options[option]['default'])) || this.options[option] || null;
	}
	
	/**
	 *
	 *
	 */
	getStyle(attribute, stateKey = null, stateVal = 'default') {
		
		let settings = {};
		stateKey = stateKey || this.primaryStateKey;
		
		// get user setting
		settings['options'] = this._getUserSetting(stateKey, attribute + 'Style', stateVal);
		
		// validate user defined styles
		if (settings['options'] && typeof settings['options'] !== 'object') {
			settings['options'] = undefined;
		}
		
		// get function setting or fallback to defaults
		['configurations', 'defaults'].forEach(config => {
			settings[config] = this._getConfigurationSetting(config, stateKey.toLowerCase(), attribute + 'Style', stateVal);
		});
		
		return super._getValueFromSettings(settings, {});
	}
	
	/**
	 *
	 *
	 */
	listen(eventName, callback, duplicateCheck = true) {
		
		if (typeof eventName === 'function') {
			duplicateCheck = callback !== undefined ? callback : true;
			callback = eventName;
			eventName = 'stateChange';
		}
		
		// check for duplicates
		const duplicate = duplicateCheck === true ? this.listeners(eventName).findIndex(listener => listener.toString() === callback.toString()) > -1 : false;
		
		// attach callback
		if (!duplicate) {
			this.on(eventName, (stateKey, state, device) => this.id === device.id && callback(stateKey, state, device));
		}
	}
	
	/**
	 * Requests all state values from the backend.
	 *
	 * @param	{String|Array}	[stateKey=null]
	 * @param	{Boolean}		[subscribe=false]
	 * @return	{Array<Promises>}
	 *
	 */
	requestDeviceStates(stateKeys = null, subscribe = false) {
		stateKeys = !stateKeys ? Object.keys(this.states) : Array.isArray(stateKeys) ? stateKeys : [stateKeys];
		
		let responses = [];
		stateKeys.forEach(stateKey => {
			responses.push(this.requestDeviceState(stateKey, subscribe));
		});
		
		return responses;
	}
	
	/**
	 * Requests a state value from the backend.
	 *
	 * @param	{String}	[stateKey=this.primaryStateKey]
	 * @param	{Boolean}	[subscribe=false]
	 * @return	{Promise}
	 *
	 */
	requestDeviceState(stateKey = null, subscribe = false) {
		stateKey = stateKey || this.primaryStateKey;
		
		let s = this._getStateFromKey(stateKey);
		if (typeof s === 'string') {
			return Promise.reject({ 'success': false, 'stateKey': stateKey, 'error': new Error(s) });
		}
		
		return new Promise((resolve, reject) => {
			
			// send cached value if present and subscrbe (means always updated)
			if (s.state.value !== undefined && this.stateSubscriptions.indexOf(stateKey) > -1) {
				
				// publish state
				this.emit('stateChange', stateKey, s.state.value, this);
				
				// resolve
				resolve({ 'success': true, 'stateKey': stateKey, 'cached': true });
			}
			
			// request from backend
			else {
				this.socket.getState(s.state.node)
					.then(state => {
						
						// state does not exist
						if (state === null) {
							reject({ 'success': false, 'stateKey': stateKey, 'error': new Error('State ' + s.state.node + ' not found!') });
						}
						
						// set result
						s.state.value = this._updateDeviceState(stateKey, state);
						
						// publish state change
						this.emit('stateChange', stateKey, s.state.value, this);
						
						// subscribe to state
						if (subscribe) {
							this.subscribeDeviceState(stateKey).catch(err => reject({ 'success': false, 'stateKey': stateKey, 'error': err }));
						}
						
						// resolve
						resolve({ 'success': true, 'stateKey': stateKey, 'cached': false });
					})
					.catch(err => reject({ 'success': false, 'stateKey': stateKey, 'error': err }));
			}
		});
	}
	
	/**
	 * Requests a state value from the backend.
	 *
	 * @param	{String}	[stateKey=this.primaryStateKey]
	 * @param	{Boolean}	[subscribe=false]
	 * @param	{Object}	[options={}]
	 * @return	{Promise}
	 * @see https://github.com/ioBroker/ioBroker.history#access-values-from-javascript-adapter
	 *
	 */
	requestDeviceStateHistory(stateKey = null, subscribe = false, options = {}) {
		stateKey = stateKey || this.primaryStateKey;
		
		let s = this._getStateFromKey(stateKey);
		if (typeof s == 'string') {
			return Promise.reject({ 'success': false, 'stateKey': stateKey, 'error': new Error(s) });
		}
		
		return new Promise((resolve, reject) => {
			s.state.historyCache = s.state.historyCache || {};
			
			// send cached value if present and subscrbe (means always updated)
			if (options.start && options.end && options.step && s.state.historyCache[options.start + '#' + options.end + '#' + options.step] !== undefined) {
				
				// publish state
				this.emit('historyChange', stateKey, s.state.historyCache[options.start + '#' + options.end + '#' + options.step], this);
				
				// resolve
				resolve({ 'success': true, 'stateKey': stateKey, 'cached': true });
			}
			
			// request from backend
			else {
				this.socket.getStateHistory(s.state.node, options)
					.then(history => {
						
						// state does not exist
						if (history === null) {
							reject({ 'success': false, 'stateKey': stateKey, 'error': new Error('State ' + s.state.node + ' not found!') });
						}
						
						// cache result
						if (options.start && options.end && options.step) {
							s.state.historyCache[options.start + '#' + options.end + '#' + options.step] = history;
						}
						
						// set result
						s.state.history = history;
						
						// publish state change
						this.emit('historyChange', stateKey, s.state.history, this);
						
						// subscribe to state
						if (subscribe) {
							this.subscribeDeviceState(stateKey).catch(err => reject({ 'success': false, 'stateKey': stateKey, 'error': err }));
						}
						
						// resolve
						resolve({ 'success': true, 'stateKey': stateKey, 'cached': false });
					})
					.catch(err => reject({ 'success': false, 'stateKey': stateKey, 'error': err }));
			}
		});
	}
	
	/**
	 *
	 *
	 * @param	{String}	value
	 * @param	{String}	[stateKey=this.primaryStateKey]
	 * @return	void
	 */
	setDeviceState(stateKey = null, value) {
		stateKey = stateKey || this.primaryStateKey;
		
		let s = this._getStateFromKey(stateKey, 'action');
		if (typeof s == 'string') {
			return Promise.reject({ 'success': false, 'stateKey': stateKey, 'error': new Error(s) });
		}
		
		return new Promise((resolve, reject) => {
			this.socket.setState(s.action.node, value)
				.then(res => {
				
					// get state value if not subscribed
					if (this.stateSubscriptions.indexOf(stateKey) === -1) {
						this.requestDeviceState(stateKey);
					}
					
					// resolve
					resolve({ 'success': true, 'stateKey': stateKey });
				})
				.catch(err => reject({ 'success': false, 'stateKey': stateKey, 'error': err }));
		});
	}
	
	/**
	 *
	 *
	 * @param	{String}	[stateKey=this.primaryStateKey]
	 * @return	void
	 */
	subscribeDeviceState(stateKey = null) {
		stateKey = stateKey || this.primaryStateKey;
		
		let s = this._getStateFromKey(stateKey);
		if (typeof s == 'string') {
			return Promise.reject({ 'success': false, 'stateKey': stateKey, 'error': new Error(s) });
		}
		
		// add subscription	
		if (this.stateSubscriptions.indexOf(stateKey) === -1) {
			this.stateSubscriptions.push(stateKey);
		
			// subscribe
			return new Promise(resolve => {
				this.socket.subscribeState(s.state.node, (id, state, params) => {
					
					if (state && state.val !== this.getDeviceState(stateKey, 'val')) {
						
						// format value
						s.state.value = this._updateDeviceState(stateKey, state);
						
						// publish state change
						this.emit('stateChange', stateKey, s.state.value, this);
						
						// publish to children as well
						this.children.forEach(child => child.emit('stateChange', stateKey, s.state.value, this));
					}
				});
				
				resolve({ 'success': true, 'stateKey': stateKey });
			});
		}
		else {
			return Promise.reject({ 'success': false, 'stateKey': stateKey, 'error': new Error('State already subscribed!') });
		}
	}
	
	/**
	 *
	 *
	 */
	unsubscribeDeviceState(stateKey = null) {
		stateKey = stateKey || this.primaryStateKey;
		
		let s = this._getStateFromKey(stateKey);
		if (typeof s == 'string') {
			return Promise.reject({ 'success': false, 'stateKey': stateKey, 'error': new Error(s) });
		}
		
		// remove subscription	
		if (this.stateSubscriptions.indexOf(stateKey) > -1) {
			this.stateSubscriptions = this.stateSubscriptions.filter((value, index, arr) => value !== stateKey);
		
			// unsubscribe
			return new Promise(resolve => {
				this.socket.unsubscribeState(s.state.node);
				resolve({ 'success': true, 'stateKey': stateKey });
			});
		}
		else {
			return Promise.reject({ 'success': false, 'stateKey': stateKey, 'error': new Error('State not subscribed!') });
		}
	}
	
	/**
	 *
	 *
	 */
	unlisten(eventName, callback) {
		
		if (typeof eventName === 'function') {
			callback = eventName;
			eventName = 'stateChange';
		}
		
		this.off(eventName, callback);
	}
}
