import Log from './Log'
import i18n from './i18n'

const _copy = require('rfdc')();


/**
 * Settings
 *
 *
 */
export default class Settings {

	/*
	 * holds the layout
	 */
	static layout = [];
	
	/*
	 * holds the settings
	 */
	static settings = {};
	
	/**
	 * Apply settings.
	 *
	 * @param	{Function}	reRender
	 * @return	void
	 */
	static applySettings() {
		
		// language
		i18n.setTranslations(language => require('../i18n/' + language));
		if (Settings.settings.language) {
			i18n.setLanguage(Settings.settings.language);
		}
		
		// page title
		if (Settings.settings.pageTitle) {
			document.title = Settings.settings.pageTitle;
		}
		
		// favicon
		if (Settings.settings.pageFavicon && Settings.settings.pageFavicon.substr(0, 5) === 'data:') {
			document.getElementById('favicon').href = Settings.settings.pageFavicon;
		}
		
		// set user specific translations
		if (Settings.settings.translations) {
			
			let translations = Settings.settings.translations;
			for (let language in translations) {
				i18n.setTranslation(language, translations[language]);
			}
		}
	}
	
	/**
	 * Get params from URL.
	 *
	 * @param	{String}	url
	 * @return	{String}
	 */
	static setParamsFromUrl(url) {
		
		let baseUrl = url.substr(0, url.indexOf('#'));
		let allParams = url.substr(url.indexOf('#')+1);
		let params = null;
		
		if (allParams.indexOf('=') > -1) {
			params = {}
			
			let [ key, value ] = [ null, null ];
			allParams.split('&').forEach(param => {
				[ key, value ] = param.split('=');
				params[key] = value;
			});
			
			params = { ...Settings.getSetting('params') || {}, ...params }
			Settings.setSetting('params', params, true);
			
			if (params['keepParams'] !== 'true' && Settings.getSettingFromStorage('params') !== undefined && Settings.getSettingFromStorage('params') !== null) {
				window.history.pushState('', '', baseUrl);
			}
		}
		
		return params;
	}
	
	/**
	 * Gets a setting from local storage.
	 *
	 * @param	{String}	settings
	 * @return	{String}
	 */
	static getSettingFromStorage(setting) {
		
		try {
			return JSON.parse(window.localStorage.getItem(setting));
		}
		catch(err) {
			Log._('Settings', i18n.t('Could not retrieve setting ' + setting + ' from localStorage') + '!', 'error');
		}
		
		return null;
	}
	
	/**
	 * Gets a setting.
	 *
	 * @param	{String}	settings
	 * @return	{String}
	 */
	static getSetting(setting) {
		let gotSetting = Settings.settings[setting];
		
		if (!gotSetting) {
			gotSetting = Settings.getSettingFromStorage(setting);
		}
		
		return gotSetting || '';
	}
	
	/**
	 * Gets all settings.
	 *
	 * @param	void
	 * @return	{Object}
	 */
	static getSettings(copy = true) {
		return copy ? _copy(Settings.settings || {}) : (Settings.settings || {});
	}
	
	/**
	 * Gets the layout.
	 *
	 * @param	void
	 * @return	{Object}
	 */
	static getLayout(copy = true) {
		return copy ? _copy(Settings.layout || {}) : (Settings.layout || {});
	}
	
	/**
	 * Sets a setting.
	 *
	 * @param	{String}	setting
	 * @param	{String}	value
	 * @param	{Boolean}	saveToLocalStorage
	 * @return	{Boolean}
	 */
	static setSetting(setting, val, saveToLocalStorage = false) {
		
		if (setting && val !== undefined) {
			
			// save to local storage
			if (saveToLocalStorage) {
				window.localStorage.setItem(setting, JSON.stringify(val));
			}
			
			Settings.settings[setting] = val;
			return true;
		}
		
		return false;
	}

	/**
	 * Set layout.
	 *
	 * @param	{Object}	layout
	 * @return	{Boolean}
	 */
	static setLayout(layout) {
		if (layout) {
			Settings.layout = [ ...Settings.layout, ...layout || [] ];
			Log._('Settings', [ 'Set layout', Settings.layout ]);
			return true;
		}
		
		return false;
	}

	/**
	 * Set settings.
	 *
	 * @param	{Object}	settings
	 * @return	{Boolean}
	 */
	static setSettings(settings) {
		
		if (settings) {
			Settings.settings = { ...Settings.settings, ...settings || {} };
			Log._('Settings', [ 'Set settings', Settings.settings ]);
			return true;
		}
		
		return false;
	}
}
