import React from 'react'
//import clsx from 'clsx'

import DevicesConfig, { DevicesStates } from './Devices.config'
import i18n from '../../../controller/i18n'
import FUNCTIONS from '../../../functions'
import ucFirst, { moveKeyInObject } from '../../../helpers/utils'

import Grid from '@material-ui/core/Grid'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'

import IconButton from '@material-ui/core/IconButton'

import AddIcon from '@material-ui/icons/Add'
import ArrowUpIcon from '@material-ui/icons/KeyboardArrowUp'
import ArrowDownIcon from '@material-ui/icons/KeyboardArrowDown'
import DeleteIcon from '@material-ui/icons/Delete'

import Dialog from '../../../components/Dialog'
import Inputs from '../../../components/Inputs'


/*
 * STYLES
 */
import { withStyles } from '@material-ui/core/styles'
const styles = theme => ({
	fullWidth: {
		width: '100%',
	},
	iconContainer: {
		flexDirection: 'column',
		alignItems: 'center'
	},
	alignRight: {
		display: 'flex',
		justifyContent: 'flex-end',
		
		'& > :not(:first-child)': {
			marginLeft: 8,
		}
	}
});


class DevicesPage extends React.Component {
	
	constructor(props) {
		super(props);
		
		this.state = {
			device: this.props.device,
			errors: {}
		}
		
		this.editor = {};
		this.newStateKey = '';
	}
	
	/*
	 *
	 */
	componentDidMount() {
		this._onChange({ error: !this.state.device.name, id: 'device-name' }, this.state.device.name || ''); // trigger initial validation
	}
	
	/*
	 *
	 */
	_onChange = (props, val) => {
		
		// error occured
		if (props.error) {
			this.setState(prevStates => ({
				errors: {
					...prevStates.errors,
					[props.id]: props.error
				}
			}));
		}
		
		// no error -> remove old error
		else if (!props.error && this.state.errors[props.id]) {
			this.setState(prevStates => {
				let nextErrors = { ...prevStates.errors || {} }
				delete nextErrors[props.id];
				return { errors: nextErrors };
			});
		}
		
		// set value
		this.editor[props.id] = val;
	}
	
	/*
	 *
	 */
	_onSave = () => {
		let savedDevice = { ...this.state.device }
		savedDevice.options = savedDevice.options || {}
		savedDevice.attributes = savedDevice.attributes || {}
		
		for (const key in this.editor) {
			const val = this.editor[key];
			const [ type, base, characteristic ] = key.split('-');
			
			// states
			if (type === 'states') {
				if (typeof savedDevice[type][base] === 'object') {
					savedDevice[type][base][characteristic] = val;
				}
				else {
					savedDevice[type][base] = {
						state: savedDevice[type][base],
						[characteristic]: val
					}
				}
			}
			
			// options or attributes
			else if (type === 'options' || type === 'attributes') {
				savedDevice[type][base] = val;
			}
			
			// device
			else {
				savedDevice[base] = val;
			}
		}
		
		// save
		savedDevice.attributes._updated = Date.now();
		this.props.onSave && this.props.onSave(savedDevice);
	}
	
	/*
	 *
	 */
	_deleteDevice = () => this.props.onDelete && this.props.onDelete(this.props.device.id);
	
	/*
	 *
	 */
	_addState = stateKey => {
		
		// empty state key
		if (!stateKey) {
			this.setState(prevStates => ({
				errors: {
					...prevStates.errors,
					newStateKey: i18n.t('Type in a state key / identifier')
				}
			}));
		}
		
		// state key exists already
		else if (Object.keys(this.state.device.states || {}).indexOf(stateKey) > -1) {
			this.setState(prevStates => ({
				errors: {
					...prevStates.errors,
					newStateKey: i18n.t('State key already used')
				}
			}));
		}
		
		// add state key
		else {
			let key = stateKey.replace(/ /g, '');
			
			this.setState(prevStates => {
				let nextErrors = { ...prevStates.errors }
				delete nextErrors['newStateKey'];
				
				return {
					errors: nextErrors,
					device: {
						...prevStates.device,
						states: {
							[key]: {},
							...prevStates.device.states || {}
						}
					}
				}
			});
		}
	}
	
	/*
	 *
	 */
	_moveState = (action, key) => {
		
		this.setState(prevState => ({
			device: {
				...prevState.device,
				states: moveKeyInObject({ ...this.state.device.states }, key, action)
			}
		}));
	}
	
	/*
	 *
	 */
	_deleteState = key => {
		
		this.setState(prevStates => {
			let nextDevice = { ...prevStates.device }
			delete nextDevice.states[key];
			
			return {
				device: nextDevice
			}
		});
	}
	
	/*
	 *
	 */
	renderDeviceState(state) {
		const { classes } = this.props;
		
		const states = Object.keys(DevicesStates).map(key => {
			const s = DevicesStates[key];
			const Input = Inputs[s.type || 'Text'];
			return (
				<Inputs.FieldsetItem columns={4} key={'states-' + state.key + '-' + key}>
					<Input
						{...s}
						id={'states-' + state.key + '-' + key}
						shrink={true}
						label={s.label || ucFirst(key)}
						
						val={!state[key] || typeof state[key] === 'string' ? (state[key] || '') : (state[key].node || JSON.stringify(state[key]))}
						onChange={(props, val) => typeof s.onChange === 'function' ? s.onChange(props, val, this._onChange) : this._onChange(props, val)}
						/>
						
				</Inputs.FieldsetItem>
			);
		});
		
		return (
			<ListItem key={'StatesListItemLabel-' + state.key} dense divider>
				{state.key &&
				<ListItemIcon classes={{ root: classes.iconContainer }}>
					<IconButton size="small" onClick={() => this._moveState('up', state.key)}>
						<ArrowUpIcon />
					</IconButton>
					<IconButton size="small" onClick={() => this._moveState('down', state.key)}>
						<ArrowDownIcon />
					</IconButton>
					<IconButton size="small" onClick={() => this._deleteState(state.key)} style={{ marginTop: 40 }}>
						<DeleteIcon />
					</IconButton>
				</ListItemIcon>}
				
				<ListItemText disableTypography>
					<div style={{ paddingBottom: 8 }}><strong>{state.key}</strong></div>
					<Inputs.Fieldset noPadding>
						{states}
					</Inputs.Fieldset>
				</ListItemText>
			</ListItem>
		);
	}
	
	/*
	 *
	 */
	renderDeviceStates(states) {
		const { classes } = this.props;
		
		const renderedStates = Object.keys(states || {}).map(stateKey => {
			let state = states[stateKey];
			state = typeof state === 'string' ? { 'state': state } : state;
			return this.renderDeviceState({ ...state, key: stateKey });
		});
		
		return (
			<>
				<List classes={{ root: classes.fullWidth }} dense>
					<ListItem key={'StatesListItemLabel-add'} dense divider>
						<ListItemIcon>
							<IconButton size="small" disabled>
								<AddIcon />
							</IconButton>
						</ListItemIcon>
						
						<ListItemText disableTypography>
							<Inputs.Text id={'states-add'} onChange={(props, input) => this.newStateKey = input} shrink={true} label='new state key' val={this.newStateKey} />
							<Inputs.Button
								label='add state'
								onClick={() => this._addState(this.newStateKey)}
								/>
							
							{this.state.errors['newStateKey'] || ''}
						</ListItemText>
					</ListItem>
					
					{renderedStates}
					
				</List>
			</>
		);
	}
	
	/*
	 *
	 */
	renderDeviceOption(config, value) {
		
		if (Array.isArray(value)) {
			value = value.join(',');
		}
		else if (typeof value === 'object') {
			value = JSON.stringify(value);
		}
		
		const Tag = Inputs[config.type || 'Text'];
		return (
			<ListItem key={'StatesListItemLabel-' + config.parameter} divider>
				<ListItemText
					primary={config.parameter}
					secondary={i18n.t(config.label || '')}
					/>
					
				<ListItemSecondaryAction>
					<Tag
						key={'options-' + config.parameter}
						id={'options-' + config.parameter}
						
						val={value}
						onChange={this._onChange}
						{...config}
						/>
					
				</ListItemSecondaryAction>
			</ListItem>
		);
	}
	
	/*
	 *
	 */
	renderDeviceOptions(options) {
		const { classes } = this.props;
		const renderedOptions = Object.keys(DevicesConfig).sort().map(config => this.renderDeviceOption({ ...DevicesConfig[config], parameter: config }, options[config] || ''));
		
		return (
			<List classes={{ root: classes.fullWidth }} dense>
				{renderedOptions}
			</List>
		);
	}
	
	/*
	 *
	 */
	render() {
		const { classes } = this.props;
		
		if (this.state.device === null) {
			return null;
		}
		
		const states = this.renderDeviceStates(this.state.device.states);
		const options = this.renderDeviceOptions(this.state.device.options);
		const stateKeys = Object.keys(this.state.device.states);
		
		return (
			<Dialog
				header={i18n.t('Device Configuration') + ' (' + i18n.t('Device ID') + ': ' + this.state.device.id + ')'}
				actions={[
					<Grid key='actions' container>
						<Grid item xs={6}>
							{this.state.device.name && <Inputs.Button key='action-delete' variant="text" onClick={() => this._deleteDevice()} color="secondary" startIcon={<DeleteIcon />} label="Delete Device" />}
						</Grid>
						<Grid item xs={6} className={classes.alignRight}>
							<Inputs.Button key='action-cancel' variant="text" onClick={() => this.props.onClose()} color="secondary" label="Cancel" />
							<Inputs.Button key='action-save' variant="text" onClick={() => this._onSave()} color="primary" disabled={Object.keys(this.state.errors).length > 0} label="Apply" />
						</Grid>
					</Grid>
				]}
				
				dense
				maxWidth='md'
				fullWidth={true}
				
				open={true}
				onClose={() => this.props.onClose()}
				>
				
				<Inputs.Fieldset label={'General'}>
					<Inputs.FieldsetItem columns={4}>
						<Inputs.Text
							key="device-name"
							id="device-name"
							autoFocus
							required
							
							label='Device'
							val={this.state.device.name}
							onChange={this._onChange}
							/>
						
					</Inputs.FieldsetItem>
					<Inputs.FieldsetItem columns={4}>
						<Inputs.Select
							key="device-function"
							id="device-function"
							
							label='Function'
							val={this.state.device.function || 'other'}
							selection={Object.keys(FUNCTIONS).sort().map(func => func !== '_defaults' ? ({ [func]: i18n.t(func) }) : ({ 'other': i18n.t('other') }))}
							onChange={(props, val) => {
								
								// add default state keys of function (if not added already)
								if (FUNCTIONS[val] !== undefined) {
									Object.keys(FUNCTIONS[val]).forEach(stateKey => {
										
										if (stateKeys.indexOf(stateKey) === -1) {
											this._addState(stateKey);
										}
									});
								}
								
								this._onChange(props, val);
							}}
							/>
						
					</Inputs.FieldsetItem>
					
				</Inputs.Fieldset>
				
				<Inputs.Fieldset label='States'>
					{states}
				</Inputs.Fieldset>
				
				<Inputs.Fieldset label='Options'>
					{options}
				</Inputs.Fieldset>
			</Dialog>

		)
	}
}

export default withStyles(styles)(DevicesPage);
