import React from 'react'

import Button from '@material-ui/core/Button'
import ButtonGroup from '@material-ui/core/ButtonGroup'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import ListItemText from '@material-ui/core/ListItemText'
import StateListItem from './StateListItem'

import Devices from '../../controller/Devices'
import i18n from '../../controller/i18n'
import Log from '../../controller/Log'


/*
 * STYLES
 */
import { withStyles } from '@material-ui/core/styles';
const styles = theme => ({
	defaultListSectionDivider: {
		margin: '5px 0 0 0'
	},
	defaultListSectionDividerItem: {
		margin: '8px 0 4px 0',
		padding: '0 16px !important'
	},
	defaultListSectionText: {
		margin: 0,
		[theme.breakpoints.down('md')]: {
			margin: '0 0 0 10px',
		}
	},
	horizontalList: {
		display: 'flex',
		flexDirection: 'row',
		flexWrap: 'wrap'
	}
});


const ListGroup = withStyles(styles)(props => {
	Log._('StateList', [ 'RENDER ListGroup', props ], 'development');
	const { classes, title, actions } = props;
	
	const handleChangeOn = () => handleChange(true);
	const handleChangeOff = () => handleChange(false);
	const handleChange = targetStatus => {
		
		let status = targetStatus;
		const deviceList = props.getDevices();
		Object.keys(deviceList).forEach(deviceId => {
			const { device, options } = deviceList[deviceId];
			
			if (options.primaryStateKey === 'level') {
				status = status ? (device.getOption('defaultValue') || 100) : 0;
			}
			
			device.setDeviceState(options.primaryStateKey, status).catch(res => Log._('StateList', res.error.message, 'error', true, res.error.stack));
		});
	}
	
	return (
		<React.Fragment>
			<ListItem divider className={classes.defaultListSectionDivider} />
			<ListItem className={classes.defaultListSectionDividerItem}>
				<ListItemText
					className={classes.defaultListSectionText}
					primary={title}
					/>
				
				<ListItemSecondaryAction>
				{actions === null || actions === false ? null :
					<ButtonGroup color="primary" size="small">
						<Button onClick={handleChangeOn}>{i18n.t('on')}</Button>
						<Button onClick={handleChangeOff}>{i18n.t('off')}</Button>
					</ButtonGroup>
				}
				
				</ListItemSecondaryAction>
			</ListItem>
		</React.Fragment>
	
	);
});


class StateList extends React.Component {
	
	constructor(props) {
		super(props);
		
		this.groups = {};
	}
	
	
	render() {
		Log._('StateList', [ 'RENDER render()', this.props ], 'development');
		const { classes, devices, ...otherProps } = this.props;
		
		let group = null;
		const items = Devices.forEachDevice(devices, (device, index, options) => {
			
			// group
			if (device.type === 'group') {
				group = device.id;
				
				return <ListGroup
							key={'ListGroup-' + index + '-' + device.id}
							title={device.name}
							getDevices={() => this.groups[device.id]}
							actions={device.actions || (this.props.config.actions !== false) || false}
							/>
			}
			
			// divider
			else if (device.type === 'divider') {
				return <ListItem key={'ListDivider-' + index + '-' + device.id} divider className={classes.defaultListSectionDivider} />
			}
			
			// device
			this.groups[group] = this.groups[group] || [];
			this.groups[group][device.id] = this.groups[group][device.id] || { device, options };
			
			return <StateListItem
						{...otherProps}
						key={'StateListItem-' + index + '-' + device.id}
						
						config={{
							...otherProps.config,
							divider: this.props.config.divider && index > 0
						}}
						
						device={device}
						options={options}
						/>
		});
		
		return (
			<List dense={true} className={this.props.config.horizontal && classes.horizontalList}>
				{items}
			</List>

		);
	}
}

export default withStyles(styles)(StateList);
