import React from 'react'

import CircularProgress from '@material-ui/core/CircularProgress'
import Grid from '@material-ui/core/Grid'
import Skeleton from '@material-ui/lab/Skeleton'
import Typography from '@material-ui/core/Typography'

import { DEFAULTS } from './config/config'

import ErrorPage from './pages/ErrorPage'
import GridContainer from './components/GridContainer'
import StatusSnackbar from './components/StatusSnackbar'

import Connection from './controller/Connection'
import Devices from './controller/Devices'
import i18n from './controller/i18n'
import Log from './controller/Log'
import Settings from './controller/Settings'
import Jarvis from './Jarvis'


/*
 * STYLES
 */
import { ThemeProvider, withStyles } from '@material-ui/core/styles';
const styles = theme => ({
	loading: {
		margin: '10px 0 0 0',
		color: '#999'
	},
	gridItem: {
		margin: theme.spacing(1, 1, 0, 0),
		[theme.breakpoints.down('md')]: {
			marginRight: theme.spacing(0.4, 0.4, 0, 0)
		}
	}
});


class App extends React.Component {
	
    constructor(props) {
		super(props);
		
		this.state = {
			loaded: [],
			
			connected: false,
			error: false,
			errorMessage: '',
			failed: false,
			
			layout: null,
			theme: null
		}
		
		this.url = '';
		this.socket = null;
		this.timeout = null;
	}
	
	componentWillUnmount() {
		Log._('App', 'RENDER componentWillUnmount()', 'development');
		
		if (this.socket) {
			this.socket.disconnect();
			this.socket = null;
		}
		
		if (this.timeout !== null) {
			clearTimeout(this.timeout);
		}
	}
	
	componentDidMount() {
		Log._('App', 'RENDER componentDidMount()', 'development');
		const params = Settings.setParamsFromUrl(window.location.href) || Settings.getSetting('params');
		
		
		/*
		 * load function language file
		 */
		try {
			i18n.setTranslations(language => require('./i18n/' + language));
		}
		catch(err) {
			//console.error(err);
		}
		
		
		/*
		 * show jarvis version
		 */
		const jarvisInfo = require('../package.json');
		Log._('App', i18n.t('Welcome to jarvis %version').replace('%version', jarvisInfo.version));
		
		
		/*
		 * connect and get connection
		 */
		let urls = [ window.location.href ];
		
		// overwrite URL from parameters
		if (params.socketPort !== undefined) {
			urls = [ (params.socketSecure === 'true' ? 'https://' : 'http://') + (params.socketHost || window.location.hostname) + ':' + (params.socketPort || window.location.port) ];
			Log._('Connection', i18n.t('Connection URL (%url) taken from cache').replace('%url', urls[0]) + '.');
		}
		
		// add more possibilities
		else {
			urls.push('http://' + (params.socketHost || window.location.hostname) + ':8082'); // default web adapter port with http://
			urls.push('https://' + (params.socketHost || window.location.hostname) + ':8082'); // default web adapter port with https://
			urls.push('http://' + (params.socketHost || window.location.hostname) + ':8084'); // default socket-io adapter port with http://
			urls.push('https://' + (params.socketHost || window.location.hostname) + ':8084'); // default socket-io adapter port with https://
		}
		
		Connection.discover(urls)
			.then(connection => {
				this.socket = connection.socket;
				this.url = connection.url;
				this.setState({ connected: true });
				
				// listeners
				this.socket.on('connect', reason => this.setState({ connected: true, error: false, errorMessage: '' }));
				this.socket.on('reconnect', reason => this.setState({ connected: 'pending', error: false, errorMessage: '' }));
				this.socket.on('disconnect', reason => this.setState({ connected: false, error: true, errorMessage: reason }));
				this.socket.on('error', error => this.setState({ connected: false, error: true, errorMessage: error.message }));
				
				/*
				 * retrieve settings
				 */
				this.socket.getState(DEFAULTS.NODE_SETTINGS)
					.then(state => {
						let settings = null;
						
						try {
							settings = JSON.parse(state.val || {});
							Settings.setSettings(settings);
							Settings.applySettings();
							
							this.setState(prevState => ({ loaded: [ ...prevState.loaded, 'settings' ] }));
						}
						catch(err) {
							Log._('App', 'GET_SETTINGS: ' + err.message, 'error');
							this.setState(prevState => ({
								loaded: [ ...prevState.loaded, 'settings' ],
								error: true,
								errorMessage: i18n.t('Error parsing settings') + '!'
							}));
						}
					})
					.catch(err => {
						Log._('App', 'GET_SETTINGS: ' + err.message, 'error');
						this.setState(prevState => ({
							loaded: [ ...prevState.loaded, 'settings' ],
							error: true,
							errorMessage: typeof err === 'string' ? i18n.t(err) : i18n.t(err.message)
						}));
					});
				
				
				/*
				 * retrieve layout
				 */
				this.socket.getState(DEFAULTS.NODE_LAYOUT)
					.then(state => {
						let layout = null;
						
						try {
							layout = JSON.parse(state.val || []);
							Settings.setLayout(layout);
							
							if (layout.length === 0) {
								this.setState(prevState => ({
									loaded: [ ...prevState.loaded, 'layout' ],
									error: true,
									errorMessage: i18n.t('Please create a configuration. See documentation.')
								}));
							}
							else {
								this.setState(prevState => ({
									layout: layout,
									loaded: [ ...prevState.loaded, 'layout' ]
								}));
							}
						}
						catch(err) {
							Log._('App', 'GET_LAYOUT: ' + err.message, 'error');
							this.setState(prevState => ({
								loaded: [ ...prevState.loaded, 'layout' ],
								error: true,
								errorMessage: i18n.t('Error parsing layout') + '!'
							}));
						}
					})
					.catch(err => {
						Log._('App', 'GET_LAYOUT: ' + err.message, 'error');
						this.setState(prevState => ({
							loaded: [ ...prevState.loaded, 'layout' ],
							error: true,
							errorMessage: typeof err === 'string' ? i18n.t(err) : i18n.t(err.message)
						}));
					});
				
				
				/*
				 * retrieve devices
				 */
				this.socket.getState(DEFAULTS.NODE_DEVICES)
					.then(state => {
						
						try {
							Devices.setDevices(JSON.parse(state.val || {})).then(res => {
								Log._('App', [ 'Processed ' + res.processedDevices + ' devices and discarded ' + (res.processedDevices - res.addedDevices) + ' devices due to incorrect configuration.', res ]);
								this.setState(prevState => ({ loaded: [ ...prevState.loaded, 'devices' ] }));
							});
						}
						catch(err) {
							Log._('App', 'GET_DEVICES: ' + err.message, 'error');
							this.setState(prevState => ({
								loaded: [ ...prevState.loaded, 'devices' ],
								error: true,
								errorMessage: i18n.t('Error parsing devices') + '!'
							}));
						}
					})
					.catch(err => {
						Log._('App', 'GET_DEVICES: ' + err.message, 'error');
						this.setState(prevState => ({
							loaded: [ ...prevState.loaded, 'devices' ],
							error: true,
							errorMessage: i18n.t('Socket failed while getting devices') + '!'
						}));
					});
				
				
				/*
				 * retrieve ioBroker config
				 */
				this.socket.getObject('system.config', (err, data) => data && data.common && Settings.setSettings({ iobroker: data.common }));
			})
			.catch(urls => {
				this.url = urls.join(', ');
				
				this.setState({
					failed: true,
					connected: false
				});
			});
    }
	
	/*
	 *
	 */
	componentDidUpdate() {
		
		/*
		 * get theme
		 */
		if (this.state.loaded.indexOf('settings') > -1 && this.state.loaded.indexOf('theme') === -1) {
			import('./controller/theme').then(theme => {
				this.setState(prevState => ({
					loaded: [ ...prevState.loaded, 'theme' ],
					theme: theme.default
				}));
			});
		}
	}
	
	/*
	 *
	 */
	renderError(error) {
		Log._('App', 'RENDER renderError()', 'development');
		
		error = error || this.state.errorMessage;
		Log._('App', 'Render Error: ' + error, 'error');
		
		return (

<StatusSnackbar
	key={'StatusSnackbar'}
	variant="error"
	message={error.toString()}
	/>

		);
	}
	
	/*
	 *
	 */
	renderLoadingProgress() {
		Log._('App', 'RENDER renderLoadingProgress()', 'development');
		
		const { classes } = this.props;
		return (

<Grid
	key="loadingProcess"
	container
	spacing={0}
	direction="column"
	justify="center"
	alignItems="center"
	style={{ minHeight: "100vh" }}
	>

	<CircularProgress />
	<Typography className={classes.loading}>{i18n.t('Retrieving settings')}...</Typography>

</Grid>

		);
	}
	
	/*
	 *
	 */
	renderLoadingSkeleton(props) {
		Log._('App', 'RENDER renderLoadingSkeleton()', 'development');
		
		const { classes } = this.props;
		const { topBar, tabBar, gridColumns } = props;
		
		const SkeletonLoader = (props) => <Skeleton animation="wave" variant="rect" className={classes.gridItem} height={props.height} />;
		
		let gridContents = {}
		for (let column = 1; column <= gridColumns; column++) {
			gridContents[column] = gridContents[column] || [];
			
			let loaders = Math.random() * (5 - 2) + 2;
			for (let loader = 1; loader <= loaders; loader++) {
				gridContents[column].push(<SkeletonLoader key={column + '#' + loader} height={(Math.random() * (400 - 100) + 100)} />);
			}
		}
		
		return (

<React.Fragment>
	{topBar && <Skeleton animation="wave" variant="rect" height={64} />}
	{tabBar && <Skeleton animation="wave" variant="rect" height={48} />}
	<GridContainer key="loadingSkeleton" contents={gridContents} />
</React.Fragment>

		);
	}
	
	/*
	 *
	 */
    render() {
		Log._('App', 'RENDER render()', 'development');
		
		// error message
		const error = this.state.error ? this.renderError() : null;
		
		/*
		 * Connection failed, so only return error
		 */
		if (this.state.failed === true) {
			return <ErrorPage icon="mdi-lan-disconnect" message={i18n.t('Connection failed! Could not connect to %url.').replace('%url', this.url)} />
		}
		
		/*
		 * Show LoadingProgress as long as layout is unloaded
		 */
		else if (!error && this.state.loaded.indexOf('layout') === -1) {
			return this.renderLoadingProgress();
		}
		 
		/*
		 * Show LoadingSkeleton when layout is loaded but anything else is unloaded
		 */
		else if (!error && this.state.loaded.indexOf('layout') !== -1 && (this.state.loaded.indexOf('settings') === -1 || this.state.loaded.indexOf('theme') === -1 || this.state.loaded.indexOf('devices') === -1)) {
			return this.state.layout.length === 0 ? null : this.renderLoadingSkeleton({ topBar: true, tabBar: this.state.layout && this.state.layout[0] && this.state.layout[0].tabs.length > 1, gridColumns: this.state.layout && this.state.layout[0] && this.state.layout[0].tabs && this.state.layout[0].tabs[0] && this.state.layout[0].tabs[0].columns && this.state.layout[0].tabs[0].columns.length });
		}
		
		/*
		 * Show App
		 */
		Log._('App', [ 'Connected:', this.state.connected ]);
		return (

<ThemeProvider theme={this.state.theme}>
	{error}
	<Jarvis key="jarvis" connectionStatus={this.state.connected} />
</ThemeProvider>

		)
	}
}

export default withStyles(styles)(App);
