
export default {
	"_any": {
		"value": (val) => val.toString(),
		//stateStyle
		//unit
		//icon
		//iconStyle
		"display": {
			"true": "on",
			"false": "off"
		},
		"stateStyle": {
			"true": {
				"color": "#090",
				"fontWeight": "bold"
			},
			"false": {
				"color": "#999"
			}
		}
	},
	"battery": {
		"unit": " %",
		"icon": {
			">80": "battery-high",
			"<=80": "battery-medium",
			"<=30": "battery-low",
			"<=10": "battery-outline",
			"<=5": "battery-alert-variant-outline"
		}
	},
	"door": {
		"icon": {
			"true": "door-open",
			"false": "door-closed"
		},
		"display": {
			"true": "open",
			"false": "closed"
		}
	},
	"humidity": {
		"unit": " %",
		"icon": "water-percent"
	},
	"level": {
		"unit": " %"
	},
	"lowbattery": {
		"icon": {
			"true": "battery-alert-variant-outline",
			"false": "battery-high"
		},
		"display": {
			"true": "low",
			"false": "full"
		},
		"stateStyle": {
			"true": {
				"color": "#900",
				"fontWeight": "bold"
			},
			"false": {
				"color": "#999"
			}
		}
	},
	"position": {
		"icon": "map-marker"
	},
	"power": {
		"icon": "power"
	},
	"reachability": {
		"icon": {
			"true": "lan-connect",
			"false": "lan-disconnect"
		},
		"display": {
			"true": "reachable",
			"false": "not reachable"
		},
		"stateStyle": {
			"true": {
				"color": "#090"
			},
			"false": {
				"color": "#900",
				"fontWeight": "bold"
			}
		}
	},
	"temperature": {
		"unit": " °C",
		"icon": "thermometer"
	},
	"trigger": {
		"icon": "power"
	},
	"unreach": {
		"icon": {
			"true": "lan-disconnect",
			"false": "lan-connect"
		},
		"display": {
			"true": "not reachable",
			"false": "reachable"
		},
		"stateStyle": {
			"true": {
				"color": "#900",
				"fontWeight": "bold"
			},
			"false": {
				"color": "#090"
			}
		}
	},
	"wind": {
		"unit": " km/h",
		"icon": "weather-windy"
	}
}
